import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { SearchService } from '../../services/search.service';
import { IJob } from '../../interfaces/job.interface';
import { GoogleMapsApiService, JobsService } from 'src/app/services';
import {NotifierService} from 'angular-notifier';

@Component({
  selector: 'app-jobs-details',
  templateUrl: './jobs-details.component.html',
  styleUrls: ['./jobs-details.component.css']
})
export class JobsDetailsComponent implements OnInit {

  job: IJob;
  isFetched: boolean;
  hasNoCurrentJob: boolean;

  constructor(private route: ActivatedRoute,
              private jobService: JobsService ,
              private searchService: SearchService,
              private mapCorr: GoogleMapsApiService,
              private notifier: NotifierService,
  ) {
    this.searchService.fetchSearchJobDetails(`api/search/job/${this.route.snapshot.paramMap.get('id')}`)
    .subscribe(async (job: IJob) => {
      this.isFetched = true;
      if (Object.values(job).length) {
        job['location'] =
            job['latitude'] || job['longitude']
              ? await this.getLocation(job['latitude'], job['longitude'])
              : '';
        return this.job = job;
      }
      return this.hasNoCurrentJob = true;
    });
  }

  ngOnInit() {
  }
  applyJob(){

    this.jobService.applyForJob(this.job.company.slug, this.job.slug).subscribe(
      res =>{

        this.notifier.notify('success','Congrats!, You have been Applied for this job');
      },err=>{
        this.notifier.notify('error','Some thing wrong happened');
      }
    )
  }
  getLocation(longitude: any, latitude: any) {
    this.mapCorr.getLocationByCoordsEn(longitude, latitude).subscribe(
      res => {
        // FIXME: parse location http://bit.ly/2HRuOAO
        console.log('location: ', res);
      }
    );
  }
}
