import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivityBodyComponent } from '../../wall/sub-components/activity-body/activity-body.component';
import { ActivatedRoute } from '@angular/router';
import { WallService } from 'src/app/services/api/wall.service';

@Component({
  selector: 'app-saved-posts',
  templateUrl: './saved-posts.component.html',
  styleUrls: ['./saved-posts.component.css']
})
export class SavedPostsComponent implements OnInit {
  @ViewChild('talktoActivityBOOK') talktoActivityBOOK: ActivityBodyComponent;
  slug;
  admin;
  current_user
  constructor(private route: ActivatedRoute, private _wall: WallService) { }

  ngOnInit() {
    this.slug = this.route.parent.params['value']['slug'];
    this.current_user = JSON.parse(localStorage.getItem('user'));
    if (this.current_user.slug === this.slug) {
      this.admin = true;
    }
    this.getSavedPosts();
  }
  getSavedPosts() {
    this.talktoActivityBOOK.getSavedPosts();
  }
  searchSavedPosts(evt) {
    this.talktoActivityBOOK.searchSavedPosts(evt.currentTarget.value);
  }
}
