import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { WallService } from '../../../../services/api/wall.service';
import { ProfileService } from '../../../../providers/profile/profile.service';
import { NgxSpinnerService } from 'ngx-spinner';
@Component({
  selector: 'app-friends',
  templateUrl: './friends.component.html',
  styleUrls: ['../../../../../assets/css/pages/friends-list.css']
})
export class FriendsComponent implements OnInit {
  loadingFriends = true;
  current_user;
  slug;
  friendsRequests = [];
  friendsRequestsCount;
  friendsRequestsNext;
  myFriends;
  myFriendsCount;
  myFriendsNext;
  admin;
  defaultImageBath;
  constructor(private route: ActivatedRoute,
    private _httpWall: WallService, private spinner: NgxSpinnerService) {
    this.slug = this.route.snapshot.parent.paramMap.get('slug');
    this.current_user = JSON.parse(localStorage.getItem('user'));
  }

  ngOnInit() {
    document.getElementById('Friends1Tab').click();
    if (this.current_user.slug === this.slug) {
      this.admin = true;
      this.getFriends();
      this.getFriendRequests();
    } else {
      this.getFriends();
    }
  }
  searchFriends(evt) {
    const value = evt.currentTarget.value;
    this._httpWall.searchFriends(this.slug, value).subscribe((res: any) => {
      this.loadingFriends = false;
      if (res !== null) {
        this.myFriendsCount = res['count'];
        this.myFriends = res['results'];
        this.myFriendsNext = res['next'];
      } else {
        this.myFriendsCount = 0
        this.myFriends = [];
        this.myFriendsNext = 0;
      }
    });
  }
  getFriends() {
    this.spinner.show();
    this._httpWall.getFriends(this.slug)
      .subscribe((res) => {
        if (res) {
          this.myFriendsCount = res['count'];
          this.myFriends = res['results'];
          this.myFriendsNext = res['next'];
        }
        this.loadingFriends = false;
        this.spinner.hide();
      });
  }

  getFriendRequests() {
    this._httpWall.getFriendsRequests(this.current_user.slug).subscribe((res) => {
      if (res) {
        this.friendsRequests = res['results'];
        this.friendsRequestsCount = res['count'];
        this.friendsRequestsNext = res['next'];
      }
    });
  }

  unfriend(FriendSlug, index) {
    this._httpWall.unFriend(FriendSlug)
      .subscribe((res) => {
        this.myFriends.splice(index, 1);
        this.myFriendsCount -= 1;
      });
  }

  addFriend() {
    this._httpWall.addFriend(this.slug).subscribe();
  }

  rejectFriendReq(slug, index) {
    this._httpWall.rejectFriendsRequest(slug).subscribe((res) => {
      this.friendsRequests.splice(index, 1);
      this.friendsRequestsCount -= 1;
      this.getFriendRequests();
    }, () => {
      // this.errorMessage = 'some thing went wrong';
    });
  }

  acceptFriendReq(slug, index) {

    this._httpWall.acceptFriendsRequest(slug).subscribe((res) => {
      this.friendsRequests.splice(index, 1);
      this.friendsRequestsCount -= 1;
      this.getFriendRequests();
      // @Toni
      this.getFriends();
    }, () => {
      // this.errorMessage = 'some thing went wrong';
    });
  }
}

