import {AfterViewInit, Component, ElementRef, OnInit} from '@angular/core';
import {AuthService} from '../../providers/auth-login/auth.service';
import {Router} from '@angular/router';
import { SignUpModel, ValidateModel } from '../../models/interfaces';
import { ToastrComponentlessModule } from 'ngx-toastr';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['../../../assets/css/pages/wizard.css']
})
export class SignupComponent implements OnInit, AfterViewInit {
  stepClasses = {
    firstClass: '',
    secondClass: '',
    thirdClass: '',
    fourthClass: '',
  };
  defaultImageBath;
  successMessage;
// {
//   "username":"123123",
//   "email":"123213@hotmail.com",
//   "password1":"ahmednano2011",
//   "password2":"ahmednano2011",
//   "first_name":"Ahmed",
//   "last_name":"Elemam",
//   "category":[1,2,3,4,5]
// }
  data: SignUpModel = {
    'first_name': '',
    'last_name': '',
    'username': '',
    'category': [],
    'email': '',
    'password1': '',
    'password2': '',
    'about': ''
  };
  validatedata: ValidateModel = {
    'username': '',
    'email': '',
  }
  error_messages;
  categories = [];


  constructor(private _login: AuthService,
              private router: Router) {
    if (localStorage.getItem('token')) {
      this._login.verifyToken({
        'token': localStorage.getItem('token')
      })
        .subscribe((data: any) => {
          if (data.token) {
            this.router.navigate(['/wall']);
          } else {
            localStorage.clear();
          }
        }, err => localStorage.clear());
    }
    this._login.getCategories().subscribe((res: any) => {
      this.categories = res;
    }, (err) => {
      console.error('error loading categories');
    });
  }

  ngOnInit() {
    this.currentStep(1);
  }

  ngAfterViewInit() {

  }

  selectCategory(catId, catIndex) {
    if (this.categories[catIndex]['active'] === 'active') {
      this.data.category.splice(this.data.category.indexOf(catId), 1);
      this.categories[catIndex]['active'] = '';
      this.data.category.push(catId);
    } else {
      this.categories[catIndex]['active'] = 'active';
      this.data.category.push(catId);
    }
  }

  activeToDone() {
    Object.keys(this.stepClasses).forEach((cls) => {
      if (this.stepClasses[cls] === 'active') {
        this.stepClasses[cls] = 'done';
      }
    });
  }

  currentStep(id) {
    this.error_messages = '';
    switch (id) {
      case 1:
        this.activeToDone();
        this.stepClasses.firstClass = 'active';
        break;
      case 2:
        if (this.stepClasses.firstClass !== '' &&
          this.stepClasses.secondClass !== '') {
          this.activeToDone();
          this.stepClasses.secondClass = 'active';
        }
        break;
      case 3:
        if (this.stepClasses.secondClass !== '' && this.stepClasses.thirdClass !== '') {
          this.activeToDone();
          this.stepClasses.thirdClass = 'active';
        }
        break;
      case 4:
        if (this.stepClasses.thirdClass !== '' && this.stepClasses.fourthClass !== '') {
          this.activeToDone();
          this.stepClasses.fourthClass = 'active';
        }
        break;
      default:
        console.log('Sorry, we are out of ' + id + '.');
    }
  }

  next(id) {
    switch (id) {
      case 2:
        this.validatedata.username = this.data.username;
        this.validatedata.email = this.data.email;
        this._login.verifyUser(this.validatedata)
          .subscribe((res)=>{
            if(res == '1'){
              this.error_messages = ['Username and Email already exists']        
            }
            else if (res == '0'){
              this.stepClasses.secondClass = 'current';
              this.currentStep(id);
            }
          });   
        break;
      case 3:
        this.stepClasses.thirdClass = 'current';
        this.currentStep(id);      
        break;
      case 4:
        this._login.signUp(this.data)
          .subscribe((res) => {
              localStorage.setItem('token', res['token']);
              localStorage.setItem('user', JSON.stringify(res['user']));
              this.stepClasses.fourthClass = 'current';
              this.currentStep(id);
              this.successMessage = 'your account has been created';
              setTimeout(() => {
                this.successMessage = '';
              }, 1000);
            }
            , (err) => {            
              if (JSON.stringify(err.error).match(/^(.*email.*|.*username.*|.*password.*|.*password2.*)$/)) {
                this.currentStep(1);
                window.scroll(0, 0);
              }
              this.stepClasses.fourthClass = 'current';            
              this.error_messages = [];
              Object.keys(err.error).forEach((field) => {
                this.error_messages = [...this.error_messages, `${field} : ${err.error[field]}`];
              });
            });
        break;
      default:
        console.log('Sorry, we are out of ' + id + '.');
    }
  }

  onSubmit(form) {
  }
}
