import { Component, OnInit, HostListener } from '@angular/core';
import { NotificationService } from 'src/app/services/api/notification.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';
import { Router } from '@angular/router';

@Component({
  selector: 'app-notification',
  templateUrl: './notification.component.html',
  styleUrls: ['./notification.component.css']
})
export class NotificationComponent implements OnInit {
  notifications = [];
  months = [];
  defaultImageBath;
  last_6_Months = [];
  year = new Date().getFullYear();
  month = new Date().getMonth() + 1;
  currentMonth; currentYear; nextUrl;
  constructor(private _notifyServ: NotificationService,
    private _toastr: ToastrService,
    private router: Router) { }

  ngOnInit() {
    this.currentMonth = this.month;
    this.currentYear = this.year;
    this.getLast6Months();
    this.getNotificationsList();
  }
  getNotificationsList() {
    this._notifyServ.getNotifications()
      .subscribe((res: any) => {
        this.notifications = res['results'];
        this.nextUrl = res.next;
      }, (error: any) => {
        this._toastr.error('No Notifications exist.');
      });
  }
  markAsSeen(targetUrl) {
    this._notifyServ.markAsRead(targetUrl).subscribe(res => {
    })
  }
  getLast6Months() {
    this.months = [{ id: 1, name: "January", year: this.year }, { id: 2, name: "February", year: this.year }, { id: 3, name: "March", year: this.year }, {
      id: 4, name: "April", year: this.year
    }, { id: 5, name: "May", year: this.year },
    { id: 6, name: "June", year: this.year }, { id: 7, name: "July", year: this.year }, { id: 8, name: "August", year: this.year },
    {
      id: 9, name: "September", year: this.year
    }, {
      id: 10, name: "October", year: this.year
    }, {
      id: 11, name: "November", year: this.year
    },
    { id: 12, name: "December", year: this.year }];
    for (let i = 0; i < this.currentMonth -1; i++) {
      this.last_6_Months.push(this.months[i]);
    }
    return this.last_6_Months.reverse();
  }
  // MonthsValidation(numberOfMonths) {
  //   for (let i = 0; i < numberOfMonths; i++) {
  //     this.last_6_Months.push(this.months[i]);
  //     this.last_6_Months[this.last_6_Months.length - 1]['year'] = this.currentYear - 1;
  //   }
  // }

  filterNotifications(month, year) {
    this._notifyServ.filterNotifications(month, year).subscribe((res: any) => {
      this.notifications = res['results'];
      this.nextUrl = res.next;
    }, (error: any) => {
      this._toastr.error('No Notifications exist.');
    })
  }
  navigateNotification(url) {
    let id = url.slice(10);
    this.router.navigate([`/activity/${+id}`]);
  }
  @HostListener('window:scroll', ['$event'])
  scroll() {
    if ((window.innerHeight + window.scrollY) >= document.body.offsetHeight - 200) {
      // you're at the bottom of the page
      if (this.nextUrl) {
        const next = this.nextUrl;
        this.nextUrl = null;
        this._notifyServ.getNextNotifications(next).subscribe(
          (res: any) => {
            this.nextUrl = res.next;
            this.notifications = [...this.notifications, ...res.results];
          }
        );
      }
    }

  }
}
