import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';

import { map } from 'rxjs/operators';
import {environment} from '../../../environments/environment';


const API_URL = environment.apiUrl;


@Injectable({
  providedIn: 'root'
})
export class JobsService {
  selectedForInterviews = [];
    constructor(private http: HttpClient) { }
    get(company_slug) {
      return this.http.get(API_URL + `/companies/${company_slug}/jobs`);
    }
    getNext(url) {
      return this.http.get(url);
    }
    post(company_slug, job) {
      return this.http.post(API_URL + `/companies/${company_slug}/jobs`, job);
    }
    getJobBySlugForAdmin(company_slug, job_slug) {
      return this.http.get(API_URL + `/companies/${company_slug}/jobs/${job_slug}`);
    }
    getJobBySlugForUser(company_slug, job_slug) {
      return this.http.get(API_URL + `/companies/${company_slug}/jobs/${job_slug}/info`);
    }
    updateJob(company_slug, job_slug, job) {
      return this.http.patch(API_URL + `/companies/${company_slug}/jobs/${job_slug}`, job);
    }
    applyForJob(company_slug, job_slug) {
     return this.http.post(API_URL + `/companies/${company_slug}/jobs/${job_slug}/apply`, null);
    }
    getAppliedForJob(company_slug, job_slug) {
      return this.http.get(API_URL + `/companies/${company_slug}/jobs/${job_slug}/applied`);
    }
    postApplicant(company_slug, job_slug, user, applicant) {
      return this.http.post(API_URL + `/companies/${company_slug}/jobs/${job_slug}/shortlisted/${user}`, applicant);
    }
    getShortListed(company_slug, job_slug) {
      return this.http.get(API_URL + `/companies/${company_slug}/jobs/${job_slug}/shortlisted`);
    }
    removeFromShortListed(company_slug, job_slug, user_slug) {
      return this.http.post(API_URL + `/companies/${company_slug}/jobs/${job_slug}/un-shortlisted/${user_slug}`, null);
    }
    selectForInterview(company_slug, job_slug, user, details) {
      console.log('user ins service', user);

      // this.selectedForInterview(user.user_profile.auth_user.username,details);
      return this.http.post(API_URL + `/companies/${company_slug}/jobs/${job_slug}/interview/${user.user_profile.slug}`, details);
    }
    selectedForInterview(company_slug, job_slug) {
      return this.http.get(API_URL + `/companies/${company_slug}/jobs/${job_slug}/interview`);
    }
    searchJob(search, companyId) {
      const httpOptions = {
        params: new HttpParams().set('search', search).set('company__id', companyId)
        };
      return this.http.get(API_URL + '/api/search/job/', httpOptions);
    }

}

