import {Component, OnInit} from '@angular/core';
import {AuthService} from '../../providers/auth-login/auth.service';

@Component({
  selector: 'app-reset',
  templateUrl: './reset.component.html',
})
export class ResetComponent implements OnInit {
  mailSent = false;
  data = {
    email: ''
  };

  constructor(private _login: AuthService) {
  }

  ngOnInit() {
  }

  onSubmit(form) {
    this._login.resetPassword(this.data).subscribe(() => {
      console.log('here comes the submit msg');
      this.mailSent = true;
    }, err => {
      console.log(err.error);
    });

  }
}
