import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { forkJoin } from 'rxjs';
import { WallService } from '../../../../../../services/api/wall.service';

@Component({
  selector: 'app-create-post',
  templateUrl: './create-post.component.html',
  styleUrls: [
    '../../../../../../../assets/css/pages/post-create.css',
    './create-post.component.css',
  ]
})
export class CreatePostComponent implements OnInit {
  @Input() profilePosts: any;
  @Input() admin = true;
  @Input() audioOnly = false;
  @Input() audioVideo: any;
  @Input() current_user;
  @Input() posts = [];
  @Input() photos = [];
  @Input() tracksCount;
  @Input() friends;
  @Output() loadAudioOnly = new EventEmitter();
  @Output() loadVideoOnly = new EventEmitter();
  mentions = [];
  defaultImageBath;
  uploadingContent = false;
  errorMessage;
  successMessage;
  data = {
    content: '',
    mentions: []
  };
  recentPost;
  uploadingAudio = [];
  uploadingVideo = [];
  videoFileUpload = { status: '', message: 0, filePath: '' };
  audioFileUpload = { status: '', message: 0, filePath: '' };
  constructor(private wallApiService: WallService) { }

  ngOnInit() {
  }

  shareContent() {
    // text upload
    this.uploadingContent = true;
    this.validateMentions();
    this.data.mentions = this.mentions;
    this.wallApiService.normalContent(this.data).subscribe((res: any) => {
      // media upload
      const newcontent = res;
      newcontent['activity_attachment'] = { 'IMG': [] };
      // this.posts.unshift(res);
      if (this.photos.length > 0) {
        forkJoin(this.photos.map(photo => this.wallApiService.mediaUpload(res.id, photo['formData'])))
          .subscribe(results2 => {

            newcontent['activity_attachment'] = results2;
            this.posts.unshift(this.parseposts([newcontent])[0]);
            // this.successMessage = 'you just share a post';
            // setTimeout(() => {
            //   this.successMessage = '';
            // }, 1000);
            this.uploadingContent = false;
            this.data.content = '';
            this.photos = [];
          }, (err2) => {
            this.uploadingContent = false;
            this.errorMessage = 'some thing went wrong kindly try again';
          });
      } else {
        this.posts.unshift(newcontent);
        // this.successMessage = 'you just share a post';
        // // this.posts.unshift(res);
        // setTimeout(() => {
        //   this.successMessage = '';
        // }, 1000);
        this.uploadingContent = false;
        this.recentPost = res;
        this.data.content = '';
        this.photos = [];
      }


    },
      (err) => {
        this.uploadingContent = false;
        this.errorMessage = 'some thing went wrong kindly try again';
      });
  }
  parseposts(parsedPosts) {
    parsedPosts.forEach((post, i) => {
      let IMG = [];
      let AUDIO = [];
      let VIDEO = [];
      post.activity_attachment.forEach((att) => {
        if (att.type === 'IMG') {
          IMG = IMG.concat(att);
        }
        if (att.type === 'AUDIO') {
          AUDIO = AUDIO.concat(att);
        }
        if (att.type === 'VIDEO') {
          VIDEO = VIDEO.concat(att);
        }
      });
      parsedPosts[i]['activity_attachment'] = {
        'IMG': IMG,
        'AUDIO': AUDIO,
        'VIDEO': VIDEO,
      };

    });
    return parsedPosts;
  }

  removeFile(index) {
    this.photos.splice(index, 1);
  }


  onFileChange(event) {
    if (event.target.files.length > 0) {
      const file = event.target.files[0];
      const input = new FormData();
      input.append('file', file);
      const reader = new FileReader();
      reader.readAsDataURL(file); // read file as data url
      reader.onload = (ev) => {
        let url;
        if (file.type.includes('video')) {
          url = '../../assets/vedio.png';
        } else if (file.type.includes('audio')) {
          url = '../../assets/sound.jpeg';
        } else if (file.type.includes('image')) {
          url = reader.result;
        } else {
          alert('file unsuported');
          // this.photos.splice(this.photos.length - 1, 1);
        }
        if (url) {
          this.photos.push({ 'url': url, file: file, 'formData': input });
        }

        // this.photos[this.photos.length - 1]['url'] = reader.result;
      };
    }
  }

  onFileChangeAudio(event) {
    // ;
    // this.currentState = 'final';
    if (this.audioVideo === 'audio') {
      this.uploadingAudio.push({ 'currentState': 'initial' });
    }
    if (this.audioVideo === 'video') {
      this.uploadingVideo.push({ 'currentState': 'initial' });
    }
    if (event.target.files.length > 0) {
      const file = event.target.files[0];
      const input = new FormData();
      input.append('file', file);
      const reader = new FileReader();
      reader.readAsDataURL(file); // read file as data url
      reader.onload = (ev: Event) => {
        // ;
        let url;
        if (file.type.includes('video')) {
          url = '../../assets/vedio.png';
        } else if (file.type.includes('audio')) {
          url = '../../assets/sound.jpeg';
        } else if (file.type.includes('image')) {
          url = reader.result;
        } else {
          alert('file unsuported');
          // this.photos.splice(this.photos.length - 1, 1);
        }
        if (url) {
          // ;
          if (this.audioVideo === 'audio') {
            this.audioFileUpload = { status: '', message: 0, filePath: '' };
            this.uploadingAudio[this.uploadingAudio.length - 1]['currentState'] = 'final';
            this.wallApiService.postAudio(this.current_user.slug, input)
              .subscribe((data: any) => {
                // this.uploadingAudio.splice(0, 1);
                if (data['status'] === 'progress') {
                  this.audioFileUpload = data;
                }
                if (data['id']) {
                  this.loadAudioOnly.emit('');
                }
              }, err => console.log(err));
          } else if (this.audioVideo === 'video') {
            this.uploadingVideo[this.uploadingVideo.length - 1]['currentState'] = 'final';
            this.wallApiService.postVideo(this.current_user.slug, input)
              .subscribe((data: any) => {
                // this.uploadingVideo.splice(0, 1);
                if (data['status'] === 'progress') {
                  this.videoFileUpload = data;
                }
                if (data['id']) {
                  this.loadVideoOnly.emit('');
                }
              }, err => console.log(err));
          }

        }

        // this.photos[this.photos.length - 1]['url'] = reader.result;
      };
    }
  }

  onFileChangeVideo(event) {
    // ;
    // this.currentState = 'final';
    if (this.audioVideo === 'audio') {
      this.uploadingAudio.push({ 'currentState': 'initial' });
    }
    if (this.audioVideo === 'video') {
      this.uploadingVideo.push({ 'currentState': 'initial' });
    }
    if (event.target.files.length > 0) {
      const file = event.target.files[0];
      const input = new FormData();
      input.append('file', file);
      const reader = new FileReader();
      reader.readAsDataURL(file); // read file as data url
      reader.onload = (ev: Event) => {
        // ;
        let url;
        if (file.type.includes('video')) {
          url = '../../assets/vedio.png';
        } else if (file.type.includes('audio')) {
          url = '../../assets/sound.jpeg';
        } else if (file.type.includes('image')) {
          url = reader.result;
        } else {
          alert('file unsuported');
          // this.photos.splice(this.photos.length - 1, 1);
        }
        if (url) {
          // ;
          if (this.audioVideo === 'audio') {
            this.uploadingAudio[this.uploadingAudio.length - 1]['currentState'] = 'final';
            console.log(this.uploadingAudio, 'sending media');
            this.wallApiService.postAudio(this.current_user.slug, input)
              .subscribe((data) => {
                this.uploadingAudio.splice(0, 1);
                console.log('aduio was sent');
                this.loadAudioOnly.emit();
              }, err => console.log(err));
          } else if (this.audioVideo === 'video') {
            this.uploadingVideo[this.uploadingVideo.length - 1]['currentState'] = 'final';
            this.wallApiService.postVideo(this.current_user.slug, input)
              .subscribe((data) => {
                this.uploadingVideo.splice(0, 1);
                this.loadVideoOnly.emit();
              }, err => console.log(err));
          }

        }

        // this.photos[this.photos.length - 1]['url'] = reader.result;
      };
    }
  }
  addMention = (event) => {
    this.mentions.push({ auth_user: event });
    return '@' + event.name + ' ';
  }

  validateMentions() {
    const mentions = [];
    this.mentions.forEach(mention => {
      if (this.data.content.indexOf('@' + mention.auth_user.name) !== -1) {
        mentions.push({ auth_user: mention.auth_user.pk });
      }
    });
    this.mentions = mentions;
  }
}
