
import { Injectable } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { HttpEvent, HttpHandler, HttpInterceptor, HttpRequest } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { Router } from '@angular/router';
@Injectable()
export class CustomHttpInterceptor implements HttpInterceptor {
  token;
  constructor(
    private router: Router,
    private toastr: ToastrService,
  ) { }

  intercept(
    request: HttpRequest<any>,
    next: HttpHandler
  ): Observable<HttpEvent<any>> {
    const currentUser = localStorage.getItem('token');
    // @Toni
    // How ?!! did you forgot auth/password/change/ !!!
    
    // if (currentUser && !(request.url.split('/').includes('auth'))) {
    //   request = request.clone({
    //     headers: request.headers.set('Authorization', `JWT ${currentUser}`)
    //   });
    // }
    if (currentUser) {
      request = request.clone({
        headers: request.headers.set('Authorization', `JWT ${currentUser}`)
      });
    }

    const _this = this;
    return next.handle(request).pipe(
      catchError(error => {
        let errorMessage = '';
        if (error.status === 401 && error.statusText === 'Unauthorized' && !(request.url.split('/').includes('talent-categories'))) {
          // auto logout if 401 response returned from api
          // _this.toastr.error("You're unanuthorized");
          // location.reload(true);
          this.router.navigate(['/home'], { queryParams: { returnUrl: this.router.url } });
        }
        if (error.status === 403) {
          // _this.router.navigateByUrl('/');
        } else if (error.status === 400) {
          // if (request.url.includes('auth/password/change/')) {
          //   error.error.
          // }
          if (typeof error.message === 'string') {
            if (error.error) {
              for (let prop in error.error) {
                _this.toastr.error(prop + ' ' + error.error[prop]);
              }
            }
          }
          //  else {
          //   for (const key in error.message) {
          //     if (error.message.hasOwnProperty(key)) {
          //       _this.toastr.error(error.message[key]);
          //     }
          //   }
          // }
        } else if (error.status === 500) {
          _this.toastr.error(`please contact us`);
        }
        // _this.toastr.error(error.message);
        // if (error.error instanceof ErrorEvent) {
        //   // client-side error
        //   errorMessage = `Error: ${error.message}`;
        // } else {
        //   // server-side error
        //   errorMessage = `Error Code: ${error.status}\nMessage: ${
        //     error.message
        //     }`;
        // }
        return throwError(errorMessage);
      })
    );
  }
  handleError(error) { }
}
