import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  endPoint = '/auth/login/';
  apiUrl = environment.apiUrl;

  constructor(private _http: HttpClient) {
  }


  getToken(body) {
    return this._http.post(this.apiUrl + this.endPoint, body);
  }

  verifyToken(body) {
    return this._http.post(this.apiUrl + '/api-token-verify/', body);
  }

  signUp(body) {
    return this._http.post(this.apiUrl + '/auth/registration/', body);
  }

  getCategories() {
    return this._http.get(this.apiUrl + '/api/talent-categories/');
  }

  verifyUser(body){
    return this._http.post(this.apiUrl + '/api/verifyusernameemail/', body);
  }

  signUpPatch(body) {
    return this._http.patch(this.apiUrl + '/auth/registration/', body);
  }

  resetPassword(body) {
    return this._http.post(this.apiUrl + '/password/reset/', body);
  }
  resetPasswordConfirm(body){
    return this._http.post(this.apiUrl + '/rest-auth/password/reset/confirm/' + body.uid + '/' + body.token + '/' , body);
  }
}
