import {Pipe, PipeTransform} from '@angular/core';
import {FriendModel} from '../models/friend.model';

@Pipe({
  name: 'mention'
})
export class MentionPipe implements PipeTransform {

  transform(value: string , mentions: any[]): any {
    console.log('mentions' , mentions);
    mentions.forEach(mention => {
      console.log('mentions found ? ' ,value.indexOf('@' + mention.auth_user.first_name + ' ' + mention.auth_user.last_name));
      if (value.indexOf('@' + mention.auth_user.first_name + ' ' + mention.auth_user.last_name) !== -1) {
        value = value.replace('@' + mention.auth_user.first_name + ' ' + mention.auth_user.last_name , `
        <a href="http://34.245.195.118/profile/${mention.auth_user.slug}" target="_blank">${'@' + mention.auth_user.first_name + ' ' + mention.auth_user.last_name}</a>
        `);
      }
    });
    return value;
  }
}

