import { NgModule } from '@angular/core';
import { RouterModule, Routes, PreloadAllModules } from '@angular/router';
import { AuthGuardGuard } from './providers/auth-guard/auth-guard.guard';
import { ParentAppComponent } from './parent-app/parent-app.component';
import { WallComponent } from './parent-app/wall/wall.component';
import { AdminProfileComponent } from './parent-app/profile/admin-profile/admin-profile.component';
import { FriendsComponent } from './parent-app/profile/admin-profile/friends/friends.component';
import { VerfiyEmailComponent } from './account/verfiy-email/verfiy-email.component';
import { AccParentComponent } from './account/acc-parent/acc-parent.component';
import { LoginComponent } from './account/login/login.component';
import { SignupComponent } from './account/signup/signup.component';
import { ResetComponent } from './account/reset/reset.component';
import { ErrorComponent } from './_errors';
import { AlbumsPageComponent } from './parent-app/profile/albums-page/albums-page.component';
import { PhotoComponent } from './parent-app/profile/photo/photo.component';
import { DetailsComponent } from './parent-app/profile/details/details.component';
import { VideoComponent } from './parent-app/profile/video/video.component';
import { AudiosComponent } from './parent-app/profile/audios/audios.component';
import { PostsComponent } from './parent-app/profile/posts/posts.component';
import { ActivityComponent } from './parent-app/profile/activity/activity.component';
import { SavedPostsComponent } from './parent-app/profile/saved-posts/saved-posts.component';
import { SettingsComponent } from './parent-app/settings/settings.component';
import { NotificationComponent } from './parent-app/notification/notification.component';
import { NoticationActivityComponent } from './parent-app/notification/notication-activity/notication-activity.component';
import { LandingPageComponent } from './parent-app/landing-page/landing-page.component';
import { ResetPasswordComponent } from './account/reset-password/reset-password.component' ;

const routes: Routes = [
  {
    path: '',
    component: ParentAppComponent,
    canActivate: [AuthGuardGuard],
    children: [
      {
        path: '',
        component: WallComponent
      },
     
      {
        path: 'wall',
        component: WallComponent
      },
      {
        path: 'settings',
        component: SettingsComponent
      },
      {
        path: 'profile/:slug',
        component: AdminProfileComponent,
        children: [
          {
            path: '',
            pathMatch: 'full',
            redirectTo: 'details'
          },
          {
            path: 'photos',
            component: PhotoComponent
          },
          {
            path: 'videos',
            component: VideoComponent
          },
          {
            path: 'audios',
            component: AudiosComponent
          },
          {
            path: 'posts',
            component: PostsComponent
          },
          {
            path: 'activity',
            component: ActivityComponent
          },
          {
            path: 'saved-posts',
            component: SavedPostsComponent
          },
          {
            path: 'details',
            component: DetailsComponent
          },
          {
            path: 'friends',
            component: FriendsComponent
          },
          {
            path: 'albums/:id',
            component: AlbumsPageComponent
          }
        ]
      },
      {
        path: 'search',
        loadChildren: './parent-app/search/search.module#SearchModule',
      },
      {
        path: 'company',
        loadChildren: './parent-app/company/company.module#CompanyModule',
        data: { preload: true },
      },
      {
        path: 'job',
        loadChildren: './parent-app/job/job.module#JobModule'
      },
      {
        path: 'my.notifications',
        component: NotificationComponent
      },
      {
        path: 'activity/:id',
        component: NoticationActivityComponent
      }
    ]
  },
  {
    path:'home',
    component:LandingPageComponent
  },
  {
    path: 'verify-email',
    component: VerfiyEmailComponent
  },
  {
    path: 'rest-auth/password/reset/confirm/:uid/:token',
    component: ResetPasswordComponent
  },
  {
    path: 'account',
    component: AccParentComponent,
    children: [
      {
        path: 'login',
        component: LoginComponent
      },
      {
        path: 'signup',
        component: SignupComponent
      },
      {
        path: 'reset',
        component: ResetComponent
      }
    ]
  },
  {
    path: 'error',
    component: ErrorComponent
  },
  {
    path: '**',
    redirectTo: 'home'
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {
    enableTracing: true, // <-- debugging purposes only
    preloadingStrategy: PreloadAllModules
  })],
  exports: [RouterModule]
})
export class AppRouteModule { }
