import { Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';
import { Observable } from 'rxjs';
import { FriendModel } from '../../models/friend.model';
import { HttpClient, HttpEvent, HttpErrorResponse, HttpEventType } from '@angular/common/http';
import { throwError } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
@Injectable({
  providedIn: 'root'
})
export class WallService {
  endPoint = '/activity/';
  apiUrl = environment.apiUrl;

  constructor(private _http: HttpClient) {
  }

  getNextActivity(url) {
    return this._http.get(url);
  }
  searchFriends(slug, searchValue) {
    return this._http.get<FriendModel>(this.apiUrl + `/profile/${slug}/friends?query=${searchValue}`);
  }
  getNextComments(url) {
    return this._http.get(url);
  }
  getSavedPosts() {
    return this._http.get(this.apiUrl + '/activity/saved');
  }
  searchSavedPosts(searchValue) {
    return this._http.get(this.apiUrl + `/activity/saved?search=${searchValue}`);
  }
  getNextLikes(url) {
    return this._http.get(url);
  }
  getSingleActivity(id) {
    return this._http.get(this.apiUrl + this.endPoint + id);
  }
  getActivity() {
    return this._http.get(this.apiUrl + this.endPoint);
  }

  getActivityByProfile(slug) {
    return this._http.get(this.apiUrl + `/profile/${slug}/activity`);
  }
  getAlbumDetails(slug, id) {
    return this._http.get(this.apiUrl + `/profile/${slug}/album/` + id);
  }
  getActivityBookmarked(slug) {
    return this._http.get(this.apiUrl + `/profile/${slug}/activity/bookmark`);
  }

  getFriendsRequests(slug) {
    return this._http.get(this.apiUrl + `/profile/${slug}/friends-request`);
  }

  getFriends(slug): Observable<FriendModel> {
    return this._http.get<FriendModel>(this.apiUrl + `/profile/${slug}/friends`);
  }

  acceptFriendsRequest(slug) {
    return this._http.post(this.apiUrl + `/profile/${slug}/friends/accept`, {});
  }

  unFriend(slug) {
    return this._http.delete(this.apiUrl + `/profile/${slug}/friends`, {});
  }

  addFriend(slug) {
    return this._http.post(this.apiUrl + `/profile/${slug}/friends`, {});
  }

  rejectFriendsRequest(slug) {
    return this._http.post(this.apiUrl + `/profile/${slug}/friends/reject`, {});
  }

  normalContent(body) {
    return this._http.post(this.apiUrl + this.endPoint, body);
  }

  mediaUpload(id, body) {
    return this._http.put(this.apiUrl + this.endPoint + id + '/media', body);
  }

  getComments(activityID) {
    return this._http.get(this.apiUrl + this.endPoint + activityID + '/comments');
  }

  getLikes(activityID) {
    return this._http.get(this.apiUrl + this.endPoint + activityID + '/like');
  }

  getUnlikes(activityID) {
    return this._http.get(this.apiUrl + this.endPoint + activityID + '/unlike');
  }

  postComment(activityID, body) {
    return this._http.post(this.apiUrl + this.endPoint + activityID + '/comments', body);
  }


  deleteComment(activityId, commentId) {
    return this._http.delete(this.apiUrl + this.endPoint + `/${activityId}/comments/${commentId}`);
  }
  updateComment(activityId, commentId, comment) {
    return this._http.put(this.apiUrl + this.endPoint + `/${activityId}/comments/${commentId}`, comment);
  }

  like(activityID, body) {
    return this._http.post(this.apiUrl + this.endPoint + activityID + '/like', body);
  }

  unlike(activityID, body) {
    return this._http.post(this.apiUrl + this.endPoint + activityID + '/unlike', body);
  }

  share(activityID, body) {
    return this._http.post(this.apiUrl + this.endPoint + activityID + '/share', body);
  }

  bookmark(activityID, body) {
    return this._http.post(this.apiUrl + this.endPoint + activityID + '/bookmark', body);
  }

  hidePost(activityID, body) {
    return this._http.post(this.apiUrl + this.endPoint + activityID + '/hide', body);
  }

  reportPost(activityID, body) {
    return this._http.post(this.apiUrl + this.endPoint + activityID + '/report', body);
  }

  getAudio(slug) {
    return this._http.get(this.apiUrl + `/profile/${slug}/audio`);
  }
  postAudio(slug, body) {
    return this._http.post(this.apiUrl + `/profile/${slug}/audio`, body, {
      reportProgress: true,
      observe: 'events'
    }).pipe(
      map(event => {
        switch (event.type) {

          case HttpEventType.UploadProgress:
            const progress = Math.round(100 * event.loaded / event.total);
            return { status: 'progress', message: progress };

          case HttpEventType.Response:
            return event.body;
          default:
            return `Unhandled event: ${event.type}`;
        }
      })
    );
  }
  getVideo(slug) {
    return this._http.get(this.apiUrl + `/profile/${slug}/video`);
  }
  postVideo(slug, body) {
    return this._http.post(this.apiUrl + `/profile/${slug}/video`, body, {
      reportProgress: true,
      observe: 'events'
    }).pipe(
      map(event => {
        switch (event.type) {

          case HttpEventType.UploadProgress:
            const progress = Math.round(100 * event.loaded / event.total);
            return { status: 'progress', message: progress };

          case HttpEventType.Response:
            return event.body;
          default:
            return `Unhandled event: ${event.type}`;
        }
      })
    );
  }
  // private handleError(error: HttpErrorResponse) {
  //   if (error.error instanceof ErrorEvent) {
  //     // A client-side or network error occurred. Handle it accordingly.
  //     console.error('An error occurred:', error.error.message);
  //   } else {
  //     // The backend returned an unsuccessful response code.
  //     // The response body may contain clues as to what went wrong,
  //     console.error(`Backend returned code ${error.status}, ` + `body was: ${error.error}`);
  //   }
  //   // return an observable with a user-facing error message
  //   return throwError('Something bad happened. Please try again later.');
  // }
  patchAvatar(slug, body) {
    return this._http.patch(this.apiUrl + `/profile/${slug}/avatar`, body, {
      reportProgress: true,
      observe: 'events'
    }).pipe(
      map(event => {
        switch (event.type) {

          case HttpEventType.UploadProgress:
            const progress = Math.round(100 * event.loaded / event.total);
            return { status: 'progress', message: progress };

          case HttpEventType.Response:
            return event.body;
          default:
            return `Unhandled event: ${event.type}`;
        }
      })
    );
  }
  patchCover(slug, body) {
    return this._http.patch(this.apiUrl + `/profile/${slug}/cover`, body, {
      reportProgress: true,
      observe: 'events'
    }).pipe(
      map(event => {
        switch (event.type) {

          case HttpEventType.UploadProgress:
            const progress = Math.round(100 * event.loaded / event.total);
            return { status: 'progress', message: progress };

          case HttpEventType.Response:
            return event.body;
          default:
            return `Unhandled event: ${event.type}`;
        }
      })
    );
  }
  resetCover(slug) {
    return this._http.post(this.apiUrl + `/profile/${slug}/cover/reset`, {
      "mode": "raw",
      "raw": ""
    });
  }
}