import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ActivityBodyComponent } from '../../wall/sub-components/activity-body/activity-body.component';

@Component({
  selector: 'app-posts',
  templateUrl: './posts.component.html',
  styleUrls: ['./posts.component.css']
})
export class PostsComponent implements OnInit {
  @ViewChild('talktoActivityP') talktoActivityP: ActivityBodyComponent;

  slug;
  current_user;
  admin=false;
  constructor(private route: ActivatedRoute) { }

  ngOnInit() {
    this.slug = this.route.parent.params['value']['slug'];
    this.current_user = JSON.parse(localStorage.getItem('user'));
    if (this.current_user.slug === this.slug) {
      this.admin = true;
    }
    this.talktoActivityP.loadPostsActivity();
  }

}
