import { Component, OnInit } from '@angular/core';
import { SettingsService } from 'src/app/services/api/settings.service';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';
@Component({
  selector: 'app-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['../../../assets/css/pages/setting.css', './settings.component.css']
})
export class SettingsComponent implements OnInit {
  profileData;
  firstTime = true;
  visibleNewEmail = false;
  profileSlug;
  formGroup: FormGroup;
  changePasswordForm;
  searchValue;
  newEmailForm;
  validation_messages = {
    'old_password': [
      { type: 'required', message: 'This Field is required' },
      { type: 'minlength', message: 'min length 8 char' }
    ],
    'new_password1': [
      { type: 'required', message: 'This Field is required' },
      { type: 'minlength', message: 'min length 8 char' }
    ]
  };
  searchDirty = false;
  constructor(private _settingService: SettingsService,
    private _toastr: ToastrService, private spinner: NgxSpinnerService) {
  }

  ngOnInit() {
    let userData = JSON.parse(localStorage.getItem('user'));
    this.profileSlug = userData.slug;
    this.getProfileData();
  }
  getProfileData() {
    this.spinner.show();
    this._settingService.getProfileSettings(this.profileSlug).subscribe((res: any) => {
      this.profileData = res;
      localStorage.setItem('settings', JSON.stringify(res));
      this.spinner.hide();
      if (this.firstTime) {
        this.initForms();
        this.setDate();
        this.firstTime = false;
      }
    });
  }
  saveToggleBtn(evt, objType) {
    let param;
    if (evt.target.checked) {
      param = { [objType]: true };
    } else {
      param = { [objType]: false };
    }
    this.saveData(param);
  }
  initForms() {
    this.initChangePassForm();
    this.newEmailForm = new FormGroup({
      'newEmail': new FormControl(null, [Validators.required, Validators.pattern('[a-zA-Z0-9.-_]{1,}@[a-zA-Z.-]{2,}[.]{1}[a-zA-Z]{2,}')])
    });
    this.formGroup = new FormGroup({
      response_from: new FormControl(null, { validators: [Validators.required, DateTimeValidator] }),
      response_to: new FormControl(null, { validators: [Validators.required, DateTimeValidator] })
    }, { updateOn: 'change' });
  }
  initChangePassForm() {
    this.changePasswordForm = new FormGroup({
      'old_password': new FormControl(null, [Validators.required, Validators.minLength(8)]),
      'new_password1': new FormControl(null, [Validators.required, Validators.minLength(8)]),
      'new_password2': new FormControl(null, [Validators.required, Validators.minLength(8)]),
    })
  }
  setDate() {
    this.formGroup.controls['response_from'].setValue(this.profileData.response_from);
    this.formGroup.controls['response_to'].setValue(this.profileData.response_to);
  }
  changePassword() {
    let params = {
      "new_password1": this.changePasswordForm.get('new_password1').value,
      "new_password2": this.changePasswordForm.get('new_password2').value,
      "old_password": this.changePasswordForm.get('old_password').value
    }
    this._settingService.changePassword(params).subscribe(res => {
      if (res) {
        this._toastr.success('Password Changed Successfully');
        this.changePasswordForm.reset();
      }
    }, error => {
      console.log(error);
    })
  }
  saveDateTimePicker(objType) {
    if (this.formGroup.valid) {
      let param = { [objType]: this.formGroup.value[objType] };
      this.saveData(param);
    } else {
      this._toastr.error('you have to enter the 2nd date');
      return false;
    }
  }
  saveProfilePreference(objType) {
    let param;
    if (objType === 'forFewDays') {
      param = { "response_all_time": false }
    } else {
      param = { [objType]: this.profileData[objType] }
    }
    this.saveData(param);
  }
  saveData(param) {
    this._settingService.saveProfileSettings(this.profileSlug, param).subscribe(res => {
      this._toastr.success('Saved Successfully');
      this.getProfileData();
    });
  }
  resetForm() {
    this.getProfileData();
  }
  makePrimary(email) {
    this._settingService.changePrimaryEmail(email.email).subscribe(res => {
      this._toastr.success('Updated Successfully');
      this.getProfileData();
    })
  }
  addNewEmail(evt) {
    let newEmail = this.newEmailForm.get('newEmail').value;
    let form = new FormData();
    form.append('new_email', newEmail);
    this._settingService.createEmail(form).subscribe(res => {
      this._toastr.success('Added Successfully');
      this.getProfileData()
      this.newEmailForm.reset();
      this.visibleNewEmail = false;
    });
  }
  removeEmail(email) {

  }
  sendVerificationEmail(email) {
    this._settingService.verifyEmail(email.email).subscribe(res => {
      this._toastr.success('Verified Successfully');
      this.getProfileData();
    })
  }
  getOldEmail_beforeEdit(id) {
    let oldSettings = JSON.parse(localStorage.getItem('settings'));
    return oldSettings.emails.find(x => x.id == id);
  }
  editEmail(modifiedEmail) {
    let originalEmail = this.getOldEmail_beforeEdit(modifiedEmail.id);
    this._settingService.editEmail(modifiedEmail.email, originalEmail.email).subscribe(res => {
      this._toastr.success('Saved Successfully');
    })
  }
  showTabOnSearch(keywordsChecker) {
    let returnVal = false;
    if (this.searchValue) {
      keywordsChecker.find(keyword => {
        if (this.searchValue.toLocaleLowerCase().includes(keyword)) {
          returnVal = true;
        }
      })
    } else {
      return true;
    }
    return returnVal;
  }
  activeTabOnSearch(keywordsChecker, event?) {
    let returnVal = false;
    if (this.searchValue) {
      keywordsChecker.find(keyword => {
        if (this.searchValue.toLocaleLowerCase().includes(keyword)) {
          returnVal = true;
        }
      })
    } else {
      if (event == 'pro-preference') {
        return true;
      }
    }
    return returnVal;
  }
}
export const DateTimeValidator = (fc: FormControl) => {
  const date = new Date(fc.value);
  const isValid = !isNaN(date.valueOf());
  return isValid ? null : {
    isValid: {
      valid: false
    }
  };
};