import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Router } from '@angular/router';


@Component({
  selector: 'app-user-info',
  templateUrl: './user-info.component.html',
  styleUrls: ['../../../../../assets/css/pages/sidebar-main.css', './user-info.component.css']
})
export class UserInfoComponent implements OnInit {
  @Input() current_user: { slug: any; };
  @Input() myFriendsCount: any;
  @Output() getSavedPosts = new EventEmitter();
  @Output() getMyActivity = new EventEmitter();
  settings: any;
  defaultImageBath: String;
  constructor(private router: Router) {
    this.settings = JSON.parse(localStorage.getItem('settings'));
  }
  ngOnInit() {
  }


  navigateSavedPost() {
    this.router.navigate([`profile/${this.current_user.slug}/saved-posts`]);
  }

  navigateMyactivity() {
    this.router.navigate([`profile/${this.current_user.slug}/activity`]);
  }
}
