import { Component, OnInit, HostListener, ElementRef, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { SearchService } from '../services/search.service';
import { ICompany } from '../interfaces/company.interface';
import { Router, ActivatedRoute } from '@angular/router';
import {MapSearchQuery} from '../utils/mapSearchQuery';

@Component({
  selector: 'app-companies',
  templateUrl: './companies.component.html',
  styleUrls: ['../../../../assets/css/pages/search-company.css'],

})
export class CompaniesComponent implements OnInit {
  searchCompanyForm: FormGroup;
  isFetched: boolean;
  defaultImageBath;
  companiesResultNumber: number;
  companies: ICompany[];
  hasNoContent: boolean;
  companiesNext: string;
  @ViewChild('companiesElem') companiesElem: ElementRef;
  companiesElemState: boolean;

  servicesProduction: Object[];
  dropdownSettings = {};

  @HostListener('window:scroll', ['$event'])
  scroll() {
    this.companiesElemState = this.companiesElem.nativeElement.classList.value === ' active show' ? true : false;
    if (window.innerHeight + window.scrollY >= document.body.offsetHeight) {
      // you're at the bottom of the page
      if (this.companiesNext !== null && this.companiesNext !== undefined && this.companiesElemState) {
        this.searchService.fetchNextCompanies(this.companiesNext).subscribe((companies: ICompany[]) => {
          this.companiesResultNumber += companies.length;
          if (companies.length) {
            const mapedData = companies.map((company: ICompany) => {
              if (company.avatar === null) {
                company['companyImgSrc'] = '../../../../assets/img/compay-default-small.png';
              } else {
                company['companyImgSrc'] = company.avatar['url'];
              }
              return { ...company };
            });
            this.companiesNext = mapedData[0]['companiesNext'];
            return (this.companies = [...this.companies, ...mapedData]);
          }
          return (this.hasNoContent = true);
        });
      }
    }
  }

  constructor(
    private _fb: FormBuilder,
    private searchService: SearchService,
    private router: Router,
    private route: ActivatedRoute
  ) {}

  ngOnInit() {
    this.searchService.fetchLookups().subscribe(lookups => {
      this.servicesProduction = lookups.hobbies;
    });

    this.searchCompanyForm = this._fb.group({
      name: [''],
      companySize: [''],
      tags__in: ['']
    });
    this.getCompanies();
    this.searchService.onUserSearchForCity().subscribe(city => {
      this.isFetched = false;
      this.getCompanies({ city });
    });


    this.dropdownSettings = {
      singleSelection: false,
      idField: 'id',
      textField: 'name',
      selectAllText: 'Select All',
      unSelectAllText: 'UnSelect All',
      itemsShowLimit: 3,
      allowSearchFilter: true
    };
  }

  getCompanies(searchQueryParamerters?: any): void {
    this.searchService.fetchSearchCompanies(searchQueryParamerters).subscribe((companies: ICompany[]) => {
      this.isFetched = true;
      this.companiesResultNumber = companies.length;
      if (companies.length) {
        const mapedData = companies.map((company: ICompany) => {
          if (company.avatar === null) {
            company['companyImgSrc'] = '../../../../assets/img/compay-default-small.png';
          } else {
            company['companyImgSrc'] = company.avatar['url'];
          }
          return { ...company };
        });
        this.companiesNext = mapedData[0]['companiesNext'];
        return (this.companies = mapedData);
      }
      return (this.hasNoContent = true);
    });
  }

  search() {
    console.log('init search')
    let searchQuery = this.searchCompanyForm.value;
    searchQuery = MapSearchQuery.mapSearchQuery(searchQuery);
    searchQuery['tags__in'] = Array.isArray(this.searchCompanyForm.value.tags__in)
      ? this.searchCompanyForm.value.tags__in.join('__')
      : '';
    console.log(searchQuery);
    this.getCompanies(searchQuery);
  }

  updateUrl($event) {
    $event['srcElement']['src'] = '../../../../assets/img/compay-default-small.png';
  }

  navigateToCompanyProfile(company: ICompany) {
    this.router.navigate(['../../company/company-page/', company.slug], { relativeTo: this.route });
  }

  reset() {
    this.searchCompanyForm.reset({ index: 0 });
  }
}
