import {Component, EventEmitter, Input, OnInit, Output, ViewEncapsulation} from '@angular/core';
import {WallService} from '../../../../../../services/api/wall.service';

@Component({
  selector: 'app-edit-comment',
  templateUrl: './edit-comment.component.html'
})
export class EditCommentComponent implements OnInit {
  @Input() current_user;
  @Input() postId;
  @Input() comment;
  @Input() commentId;
  @Input() isThread;
  @Input() friends;
  @Output() done = new EventEmitter();
  @Output() canceled = new EventEmitter();
  errorMessage;
  successMessage;
  defaultImageBath;
  updatingComment;
  newComment;
  emoji = false;
  mentions = [];


  constructor(private wallApi: WallService) { }

  ngOnInit() {
    this.newComment = this.comment.comment;
  }

  saveComment() {
    this.validateMentions()
    const body: any = {
      comment: this.newComment
    };
    if (this.mentions.length) {
      body.mentions = this.mentions;
    }
    this.updatingComment = true;
    this.wallApi.updateComment(this.postId , this.commentId , body).subscribe(
      res => {
        console.log(res);
        this.updatingComment = false;
        this.comment.comment = this.newComment;
        this.successMessage = 'Comment updated successfully';
        this.canceled.emit();
      }, err => {
        this.errorMessage = 'Something went wrong';
        console.log(err);
      }
    );
  }

  cancel() {
    this.canceled.emit();
  }
  upendEmoji(event) {
    this.newComment = this.newComment ? this.newComment + event.emoji.native : event.emoji.native;
    console.log(event);
  }
  addMention = (event) => {
    console.log(event);
    this.mentions.push({auth_user: event});
    console.log(this.mentions);
    return '@' + event.name + ' ';
  };
  validateMentions() {
    const mentions = [];
    this.mentions.forEach(meniton => {
      if (this.newComment.indexOf('@' + meniton.auth_user.name) !== -1) {
        mentions.push({auth_user: meniton.auth_user.pk});
      }
    });
    this.mentions = mentions;
  }
}
