import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Company } from 'src/app/models/company.model';
import { map } from 'rxjs/operators';
import { environment } from "../../../environments/environment";
import { HttpClient, HttpEvent, HttpErrorResponse, HttpEventType } from '@angular/common/http';


const API_URL = environment.apiUrl;


@Injectable({
  providedIn: 'root'
})
export class CompaniesService {

  constructor(private http: HttpClient) { }

  get(slug): Observable<Company[]> {
    return this.http.get<Company[]>(API_URL + `/profile/${slug}/companies`);
  }
  post(slug, company): Observable<Company> {
    return this.http.post<Company>(API_URL + `/profile/${slug}/companies`, company);
  }
  patchCompany(slug_company, property) {
    return this.http.patch(API_URL + `/companies/${slug_company}`, property);
  }
  deleteCompany(slug, id) {
    return this.http.delete<Company>(API_URL + `/companies/${slug}/` + id);
  }
  patchCover(slug, cover) {
    return this.http.patch(API_URL + `/company/${slug}/cover`, cover)
  }
  patchAvatar(slug, avatar) {
    return this.http.patch(API_URL + `/company/${slug}/avatar`, avatar);
  }
  getLookUps() {
    return this.http.get(API_URL + "/api/talent-categories");
    // return this.http.get(API_URL+"profile/lookups");
  }
  getCompany(slug, slug_company): Observable<Company> {
    return this.http.get<Company>(API_URL + `/companies/${slug_company}`);
  }
  switchToCompany(company_slug) {
    return this.http.post(API_URL + `/companies/${company_slug}/switch`, null);
  }
  followCompany(company_slug) {
    return this.http.post(API_URL + `/companies/${company_slug}/follow`, null);
  }
  unFollowCompany(company_slug) {
    return this.http.post(API_URL + `/companies/${company_slug}/un-follow`, null);
  }
}
