import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivityBodyComponent } from '../../wall/sub-components/activity-body/activity-body.component';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-audios',
  templateUrl: './audios.component.html',
  styleUrls: ['./audios.component.css']
})
export class AudiosComponent implements OnInit {
  @ViewChild('talktoActivityA') talktoActivityA: ActivityBodyComponent;
  audioVideo = 'audio';
  audioOnly = false;
  current_user;
  admin;
  slug;
  constructor(private route: ActivatedRoute) { }

  ngOnInit() {
    this.slug = this.route.parent.params['value']['slug'];
    this.current_user = JSON.parse(localStorage.getItem('user'));
    if (this.current_user.slug === this.slug) {
      this.admin = true;
    }
    this.audioOnly = true;
    this.talktoActivityA.loadAudioOnly(this.slug);
  }

}
