import {Component, OnInit, ViewChild} from '@angular/core';
import {WallService} from '../../services/api/wall.service';
import {ActivityBodyComponent} from './sub-components/activity-body/activity-body.component';
import { CompaniesService } from 'src/app/services';
import { Company } from 'src/app/models';
import {UserInfoComponent} from './sub-components/user-info/user-info.component';


@Component({
  selector: 'app-wall',
  templateUrl: './wall.component.html',
  styleUrls: [ '../../../assets/css/pages/post-comment.css']
})
export class WallComponent implements OnInit {
  errorMessage;
  successMessage;
  current_user;
  next;
  myFriendsCount;
  IsSavedPost = false;
  is_company=true
  @ViewChild('talktoActivity') talktoActivity: ActivityBodyComponent;
  companies:Array<Company>=[]

  constructor(
    private wallApiService: WallService,
    private companyService: CompaniesService
    ) {

    this.current_user = JSON.parse(localStorage.getItem('user'));
    this.wallApiService.getFriends(this.current_user.slug)
      .subscribe((res) => {
        if (res) {
          this.myFriendsCount = res['count'];
        }
      });
  }

  ngOnInit() {
    this.loadPostsActivity();
    this.getCompanies();
  }
  getCompanies(){
    this.companyService.get(this.current_user.slug).subscribe(
      (res:any)=>{
        console.log("res",res);
        this.companies=res.results;
      }
    )
  }
  onChangeSwitch(event){
    console.log("event", event);
    this.is_company=!this.is_company
  }

  getMyActivity(saved) {
    this.IsSavedPost = saved;
    this.talktoActivity.loadactivity();
  }

  loadPostsActivity() {
    this.talktoActivity.loadPostsActivity();
  }
}
