import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from "@angular/router";
import { AuthService } from 'src/app/providers/auth-login/auth.service';

@Component({
  selector: 'app-reset-password',
  templateUrl: './reset-password.component.html',
  styleUrls: ['./reset-password.component.css']
})
export class ResetPasswordComponent implements OnInit {

  data = {
    new_password1 : '',
    new_password2 : '',
    uid : '',
    token : ''
  }
  constructor(private route: ActivatedRoute, private _login: AuthService, private router :Router) { }

  ngOnInit() {
    this.data.uid = this.route.snapshot.paramMap.get("uid");
    this.data.token = this.route.snapshot.paramMap.get("token");
  }

  onSubmit(form: any){
    console.log(this.data);
    this._login.resetPasswordConfirm(this.data).subscribe(() => {
      this.router.navigateByUrl('/account/login');
    }, err => {
      console.log(err.error);
    });
  }

}
