import { Component, OnInit, ViewChild, Input, AfterViewInit } from '@angular/core';
import { NgbModal, NgbModalConfig } from '@ng-bootstrap/ng-bootstrap';
import { ImageCropperComponent, CropperSettings } from "ngx-img-cropper";
import { WallService } from 'src/app/services/api/wall.service';

@Component({
  selector: 'app-avatar-img',
  templateUrl: './avatar-img.component.html',
  styleUrls: ['../../../assets/css/pages/profile.css']
})
export class AvatarImgComponent implements OnInit, AfterViewInit {
  cropperSettings: CropperSettings;
  defaultImageBath;
  imageCropData: any;
  avatarData: any = {};
  loadingAvatar = false;
  file: File;
  imgUpload = { status: '', message: 0, filePath: '' };
  @Input() avatar;
  @Input() slug;
  @Input() admin;
  @ViewChild(ImageCropperComponent) cropper: ImageCropperComponent;

  constructor(private modal: NgbModal, private modalConfig: NgbModalConfig, private _httpWall: WallService) {
    this.cropperConfig();
  }
  ngOnInit() {
  }
  ngAfterViewInit() {
  }
  cropperConfig() {
    this.cropperSettings = new CropperSettings();
    this.cropperSettings.width = 100;
    this.cropperSettings.height = 100;
    this.cropperSettings.croppedWidth = 1000;
    this.cropperSettings.croppedHeight = 1000;
    this.cropperSettings.canvasHeight = 300;
    this.cropperSettings.noFileInput = true;
    this.imageCropData = {};
    this.modalConfig.backdrop = 'static';
    this.modalConfig.keyboard = false;
  }
  openModal(content) {
    const modalRef = this.modal.open(content, { centered: true, size: 'lg' });
    this.avatarData.image = this.avatar;
  }

  fileChangeListener($event, cropper: ImageCropperComponent) {
    this.cropper = cropper;
    var image: any = new Image();
    this.file = $event.target.files[0];
    var myReader: FileReader = new FileReader();
    var that = this;
    myReader.onloadend = function (loadEvent: any) {
      image.src = loadEvent.target.result;
      that.cropper.setImage(image);
    };
    myReader.readAsDataURL(this.file);
  }

  uploadAvatarPopup(avatarModal) {
    this.loadingAvatar = true;
    if (this.file) {
      const input = new FormData();
      input.append('file', this.file);
      this._httpWall.patchAvatar(this.slug, input)
        .subscribe((data: any) => {
          if (data['status'] == "progress") {
            this.imgUpload = data;
          }
          // if (data['id'] && data.avatar) {
          //   this.avatar = data.avatar.url;
          // }
          // @Toni 
          if (data.avatar) {
            this.avatar = data.avatar;
          }
          this.loadingAvatar = false;
          this.modal.dismissAll();
          this.imageCropData = {};
        }, err => this.loadingAvatar = false);
    }
  }

}
