export class MapSearchQuery {
  static mapSearchQuery(searhQueryObj) {
    for (const key in searhQueryObj) {
      if (searhQueryObj.hasOwnProperty(key)) {
        if (Array.isArray(searhQueryObj[key])) {
          searhQueryObj[key] = searhQueryObj[key].map(item => (item.id));
        }
      }
    }
    return searhQueryObj;
  }
}
