  checkIsAutorized(post, objAttr) {
    const postSettings = post.author_settings;
    if (postSettings[objAttr] === 'ALL') {
      return true;
    } else if (postSettings[objAttr] === 'COMPANY' && this.switchCompany !== null) {
      return true;
    } else if (postSettings[objAttr] === 'ONLY_FRIENDS' && post.is_friends) {
      return true;
    }
    if (postSettings.id === this.profileSettings.id) {
      return true;
    } else {
      return false;
    }
  }
