  parseposts(parsedPosts) {
    parsedPosts.forEach((post, i) => {
      let IMG = [];
      let AUDIO = [];
      let VIDEO = [];
      post.activity_attachment.forEach((att) => {
        if (att.type === 'IMG') {
          IMG = IMG.concat(att);
        }
        if (att.type === 'AUDIO') {
          AUDIO = AUDIO.concat(att);
        }
        if (att.type === 'VIDEO') {
          VIDEO = VIDEO.concat(att);
        }
      });
      parsedPosts[i]['activity_attachment'] = {
        'IMG': IMG,
        'AUDIO': AUDIO,
        'VIDEO': VIDEO,
      };
      if (post.original_activity) {
        let _IMG = [];
        let _AUDIO = [];
        let _VIDEO = [];
        post.original_activity.activity_attachment.forEach((att) => {
          if (att.type === 'IMG') {
            _IMG = IMG.concat(att);
          }
          if (att.type === 'AUDIO') {
            _AUDIO = AUDIO.concat(att);
          }
          if (att.type === 'VIDEO') {
            _VIDEO = VIDEO.concat(att);
          }
        });
        parsedPosts[i].original_activity['activity_attachment'] = {
          'IMG': _IMG,
          'AUDIO': _AUDIO,
          'VIDEO': _VIDEO,
        };
      }
    });
    return parsedPosts;
  }

