import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { WallService } from '../../../../../../services/api/wall.service';
import { EmojiService } from 'angular-emojione';

@Component({
  selector: 'app-comment',
  templateUrl: './comment.component.html',
  styleUrls: ['./comment.component.css'],
  encapsulation: ViewEncapsulation.Emulated
})
export class CommentComponent implements OnInit {
  @Input() current_user;
  @Input() comment;
  @Input() posts;
  @Input() post: any[];
  @Input() postId;
  @Input() postIndex;
  @Input() commentIndex;
  @Input() threadIndex;
  @Input() isThread;
  @Input() friends = [];
  admin = false;
  defaultImageBath;
  successMessage;
  errorMessage;
  switchCompany;
  editMood = false;
  constructor(private wallApi: WallService) {
    this.switchCompany = localStorage.getItem('switch');

  }

  ngOnInit() {
    console.log(this.comment);
  }
  checkIsAutorized(post, objAttr) {
    let postSettings = post.author_settings;
    this.current_user = JSON.parse(localStorage.getItem('user'));
    if (this.current_user.slug === post.auth_user.slug) {
      this.admin = true;
    }
    if (this.admin || postSettings[objAttr] == 'ALL') {
      return true;
    } else if (postSettings[objAttr] == 'COMPANY' && this.switchCompany !== null) {
      return true;
    } else if (postSettings[objAttr] == 'ONLY_FRIENDS' && post.is_friends) {
      return true;
    }
    return false;
  }
  deleteComment(id, threadIndex?) {
    console.log(id, threadIndex)
    this.wallApi.deleteComment(this.postId, id).subscribe(
      res => {
        console.log(res);
        console.log(this.isThread);
        if (this.isThread) {
          console.log(this.posts);
          this.posts[this.postIndex].comments[this.commentIndex].comment_thread.splice(threadIndex, 1);
          console.log(this.posts);
        } else {
          this.posts[this.postIndex].comments.splice(this.commentIndex, 1);
        }
        this.successMessage = 'comment deleted successfully';
      }, err => {
        this.errorMessage = 'Something went wrong!';
        console.log(err);
      }
    );
  }

  editComment() {
    this.editMood = true;
  }
}
