import {Pipe, PipeTransform} from '@angular/core';

@Pipe({
  name: 'timeDiff'
})
export class TimeDiffPipe implements PipeTransform {

  transform(value: any, args?: any): any {
    const now = new Date();
    const old_date = new Date(value);
    const diff = now.getTime() - old_date.getTime();
    if (diff / 1000 <= 60) {
      // second
      return `${Math.floor(diff / 1000)} sec ago`;
    } else if (diff / 60000 <= 60) {
      // minutes
      return `${Math.floor(diff / 60000)} mins ago`;
    } else if (diff / (60000 * 60) <= 24) {
      // hours
      return `${Math.floor(diff / (60000 * 60))} hrs ago`;
    } else if (diff / (60000 * 60 * 24) <= 365) {
      // days
      return `${Math.floor(diff / (60000 * 60 * 24))} days ago`;
    } else {
      // year
      return `${Math.floor(diff / (60000 * 60 * 24 * 365))} year ago`;
    }

  }

}
