import { Component, ElementRef, ViewChild, NgZone } from '@angular/core';
import { MapPlacesService, Maps } from '../services/map-places.service';
import { SearchService } from '../services/search.service';
import {} from 'googlemaps';

const place = null as google.maps.places.PlaceResult;

@Component({
  selector: 'app-search-nav',
  templateUrl: './search-nav.component.html',
  styleUrls: ['../../../../assets/css/pages/search-navbar.css']
})
export class SearchNavComponent {

  @ViewChild('search')
  public searchElementRef: ElementRef;
  savedCitySearchKeywords = [];


  public place: google.maps.places.PlaceResult;

  constructor(mapPlacesService: MapPlacesService, private ngZone: NgZone, private searchService: SearchService) {
    mapPlacesService.api.then(maps => {
      this.initAutocomplete(maps);
    });
  }

  initAutocomplete(maps: Maps) {
    const autocomplete = new maps.places.Autocomplete(this.searchElementRef.nativeElement);
    autocomplete.addListener('place_changed', () => {
      this.ngZone.run(() => {
        this.onPlaceChange(autocomplete.getPlace());
      });
    });
  }


  onPlaceChange(place: google.maps.places.PlaceResult) {
  }


  searchCity() {
    this.searchService.handleCityChangeSearch(this.searchElementRef.nativeElement.value);
    if (this.savedCitySearchKeywords.length === 3) {
      this.savedCitySearchKeywords.shift();
    }
    this.savedCitySearchKeywords.push(this.searchElementRef.nativeElement.value);
  }

}
