import { Injectable } from '@angular/core';
import { HttpClient, HttpEventType, HttpParams } from '@angular/common/http';
import { environment } from '../../../environments/environment';
import { map } from 'rxjs/operators';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ProfileService {
  endPoint = '/profile/';
  apiUrl = environment.apiUrl;

  private _profileData = new BehaviorSubject(null);
  profileData$ = this._profileData.asObservable();

  private _profileSettings = new BehaviorSubject(null);
  profileSettings$ = this._profileData.asObservable();

  constructor(private _http: HttpClient) { }
  searchCollection(type, searchValue) {
    let params = new HttpParams();
    params = params.append('search', searchValue);
    return this._http.get(this.apiUrl + `/api/search/${type}/`, { params: params })
  }
  getProfile(slug) {
    return this._http.get(this.apiUrl + `/profile/${slug}`);
  }
  syncProfileData(data) {
    this._profileData.next(data);
  }
  syncProfileSettings(data) {
    this._profileSettings.next(data);
  }
  patchProfile(slug, body) {
    return this._http.patch(this.apiUrl + `/profile/${slug}/update`, body);
  }

  getLookUps() {
    return this._http.get(this.apiUrl + `/profile/lookups`);
  }

  getProfileActivity(slug) {
    return this._http.get(this.apiUrl + `/profile/${slug}/activity`);
  }

  postHoppies(slug, body) {
    return this._http.post(this.apiUrl + `/profile/${slug}/hobbies`, body);
  }

  updateHoppies(slug, body) {
    return this._http.post(this.apiUrl + `/profile/${slug}/hobbies`, body);
  }
  deleteHoppies(slug, id) {
    return this._http.delete(this.apiUrl + `/profile/${slug}/hobbies/${id}`);
  }

  postSocial(slug, body) {
    return this._http.post(this.apiUrl + `/profile/${slug}/social`, body);
  }

  updateSocial(slug, body) {
    const id = body.id;
    delete body.id;
    delete body.user_profile;
    return this._http.put(this.apiUrl + `/profile/${slug}/social/${id}`, body);
  }

  deleteSocial(slug, id) {
    return this._http.delete(this.apiUrl + `/profile/${slug}/social/${id}`);
  }

  hideImage(activityID, body) {
    return this._http.post(this.apiUrl + '/activity/' + activityID + '/hide', body);
  }


  postTraining(slug, body) {
    return this._http.post(this.apiUrl + `/profile/${slug}/training`, body);
  }

  updateTraining(slug, id, body) {
    return this._http.put(this.apiUrl + `/profile/${slug}/training/${id}`, body);
  }

  deleteTraining(slug, id) {
    return this._http.delete(this.apiUrl + `/profile/${slug}/training/${id}`);
  }

  getAllAlbumsNames(slug) {
    return this._http.get(this.apiUrl + `/profile/${slug}/album`);
  }
  getAlbumData(slug, body) {
    return this._http.get(this.apiUrl + `/profile/${slug}/album`);
  }
  getAlbumDetails(slug, id) {
    return this._http.get(this.apiUrl + `/profile/${slug}/album/` + id);
  }
  createAlbumName(slug, body) {
    return this._http.post(this.apiUrl + `/profile/${slug}/album`, body);
  }

  GetAlbumLimit(slug) {
    return this._http.get(this.apiUrl + `/profile/${slug}/album/profile?limit=ture`);
  }
  getAllPhotos(slug) {
    return this._http.get(this.apiUrl + `/profile/${slug}/album/profile`);
  }

  updateAlbumImage(slug, activityId, body) {
    return this._http.put(this.apiUrl + `/profile/${slug}/album/${activityId}/image`, body)
  }

  uploadImage(slug, body) {
    return this._http.post(this.apiUrl + `/profile/${slug}/album/image`, body, {
      reportProgress: true,
      observe: 'events'
    }).pipe(
      map(event => {
        switch (event.type) {

          case HttpEventType.UploadProgress:
            const progress = Math.round(100 * event.loaded / event.total);
            return { status: 'progress', message: progress };

          case HttpEventType.Response:
            return event.body;
          default:
            return `Unhandled event: ${event.type}`;
        }
      })
    );
  }

  switchToUser(user) {
    return this._http.post(this.apiUrl + `/profile/${user}/switch`, null);
  }
}
