import {Component, Input, OnInit} from '@angular/core';
import {WallService} from '../../../../services/api/wall.service';

@Component({
  selector: 'app-suggested-friends-card',
  templateUrl: './suggested-friends-card.component.html',
  styleUrls: ['../../../../../assets/css/pages/sidebar-suggest.css']
})
export class SuggestedFriendsCardComponent implements OnInit {
  @Input() current_user;
  friendsSuggestions = [];
  friendsSuggestionsCount;
  friendsSuggestionsNext;
  errorMessage;
  successMessage;
  defaultImageBath;
  constructor(private wallApiService: WallService) { }

  ngOnInit() {
    this.getFriendSuggestions();
  }

  getFriendSuggestions() {
    this.wallApiService.getFriendsRequests(this.current_user.slug).subscribe((res: any) => {
      if (res) {
        this.friendsSuggestions = res.results;
        this.friendsSuggestionsCount = res.count;
        this.friendsSuggestionsNext = res.next;
      }
    });
  }

  acceptFriendSuggestion(slug, index) {

    this.wallApiService.acceptFriendsRequest(slug).subscribe((res) => {
      this.friendsSuggestions.splice(index, 1);
      this.friendsSuggestionsCount -= 1;
      this.getFriendSuggestions();
    }, () => {
      this.errorMessage = 'some thing went wrong';
    });
  }

}
