import { Component, OnInit } from '@angular/core';
import { ProfileService } from 'src/app/providers/profile/profile.service';
import { ProfileDataModel, CharacterizationDataModel } from 'src/app/models/interfaces';
import { forkJoin } from 'rxjs';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-details',
  templateUrl: './details.component.html',
  styleUrls: ['../../../../assets/css/pages/profile.css', './details.component.css']
})
export class DetailsComponent implements OnInit {
  editAbout = false;
  uploadingAbout = false;
  slug;
  uploadingEduaction = false;
  admin = false;
  current_user;
  editEducation = false;
  errorMessage;
  educationData = [];
  neweducationData = [{
    'course_name': '',
    'institute': '',
    'id': ''
  }];
  oldEducationData = [];
  profileData: ProfileDataModel;
  oldProfileData: ProfileDataModel;
  CharacterizationData: CharacterizationDataModel;
  oldCharacterizationData: CharacterizationDataModel;
  constructor(private _profile: ProfileService, private route: ActivatedRoute) { }

  ngOnInit() {
    this.getProfileData();
    this.slug = this.route.parent.params['value']['slug'];
    this.current_user = JSON.parse(localStorage.getItem('user'));
    if (this.current_user.slug === this.slug) {
      this.admin = true;
    }
  }


  deleteEducation(id, index) {
    const tempEdu = this.educationData.splice(index, 1);
    const oldtempEdu = this.oldEducationData.splice(index, 1);
    this._profile.deleteTraining(this.slug, id).subscribe((res) => {

    }, err => {
      this.educationData[index] = tempEdu[0];
      this.oldEducationData[index] = oldtempEdu[0];
    });
  }

  deleteNewEducation(id, index) {
    this.neweducationData.splice(index, 1);
  }

  cancelEducationEdit() {
    this.errorMessage = '';
    this.educationData = JSON.parse(JSON.stringify(this.oldEducationData));
    this.neweducationData = [{
      'course_name': '',
      'institute': '',
      'id': ''
    }];
    this.editEducation = false;
    // this.saveEducationData();

  }
  newEducationRaw() {
    this.neweducationData.push({
      'course_name': '',
      'institute': '',
      'id': ''
    });
  }
  mapProfileData(res) {
    this.profileData = res;
    this.oldProfileData = res;
    this.CharacterizationData = {
      'gender': res.gender,
      'age_from': res.age_from,
      'age_to': res.age_to,
      'height': res.height || { id: null },
      'weight': res.weight || { id: null },
      'build': res.build || { id: null },
      'hair': res.hair || { id: null },
      'eye': res.eye || { id: null },
      'ethnicity': res.ethnicity,
      'users_profile_hobbies': res.users_profile_hobbies
    }
    this.educationData = res['users_profile_courses'];
    this.oldEducationData = res['users_profile_courses'];
    this.oldCharacterizationData = Object.assign({}, this.CharacterizationData);
  }
  removeHobbyFromProfile(hobby) {
    console.log(hobby)
    let confirm = window.confirm('Delete hobby?');
    if (confirm == true) {
      this._profile.deleteHoppies(this.current_user['slug'], hobby.id).subscribe(res => {
        console.log(res);
        this.route.params.subscribe(params => {
          const puplicSlug = params['slug'];
          if (puplicSlug) {
            this.updateProfileData();
          }
        });
      }), error => {
        console.log(error);
      }
    } else {
      return 0;
    }
  }
  getProfileData() {
    this._profile.profileData$.subscribe(res => {
      if (res) {
        this.mapProfileData(res);
      }
    })
  }
  saveAbout() {
    this.uploadingAbout = true;
    this._profile.patchProfile(this.slug, { 'about': this.profileData.about })
      .subscribe((res: any) => {
        this.uploadingAbout = false;
        console.log(res);
        this.editAbout = false;
        if (res) {
          this.oldProfileData.about = res.about;
        }
      }, err => this.uploadingAbout = false);
  }
  saveEducationData() {
    this.errorMessage = '';
    let err;
    this.uploadingEduaction = true;
    this.neweducationData.forEach((edu, index) => {
      // if (!(edu['course_name'] && edu['institute'])) {
      //   this.neweducationData.splice(index, 1);
      // }
      if ((edu['course_name'] || edu['institute']) && !(edu['course_name'] && edu['institute'])) {
        err = true;
        edu['educationErrorIndex'] = true;
      }
    });
    this.educationData.forEach((edu, index) => {
      // if (!(edu['course_name'] && edu['institute'])) {
      //   this.neweducationData.splice(index, 1);
      // }
      if ((edu['course_name'] || edu['institute']) && !(edu['course_name'] && edu['institute'])) {
        err = true;
        edu['educationErrorIndex'] = true;
      }
    });
    if (err) {
      this.uploadingEduaction = false;
      this.errorMessage = 'all red fields are mandatory';
    } else {
      const updateEducation = this.educationData.map(edu => this._profile.updateTraining(this.slug, edu['id'], edu));
      const createEdu = this.neweducationData.map(edu => this._profile.postTraining(this.slug, edu));
      forkJoin([...updateEducation, ...createEdu]).subscribe(res => {
        this.editEducation = false;
        this.uploadingEduaction = false;
        this.neweducationData = [{
          'course_name': '',
          'institute': '',
          'id': ''
        }];
        this.updateProfileData()
      },
        error => {
          this.editEducation = false;
          // this.editEducation = false;
          this.uploadingEduaction = false;
          // console.error(error);
          // this.social = Object.assign({}, this.oldSocial);
        });
    }
  }
  updateProfileData() {
    // this.loadingProfile = true;
    this._profile.getProfile(this.slug).subscribe((res: any) => {
      // this.loadingProfile = false;
      this.mapProfileData(res);
    });
  }
}
