import { Component, OnInit } from '@angular/core';
import { SearchService } from './services/search.service';
import { IPerson } from './interfaces/person.interface';
import { ICompany } from './interfaces/company.interface';
import { IJob } from './interfaces/job.interface';
import { Router } from '@angular/router';
import { Capability } from 'protractor';

@Component({
  selector: 'app-search-landing',
  templateUrl: './search-landing.component.html',
  styleUrls: ['../../../assets/css/pages/search-landing.css']
})
export class SearchLandingComponent implements OnInit {
  isFetched: boolean;
  hasNoContent: boolean;
  defaultImageBath;
  peopleResultNumber: number;
  companiesResultNumber: number;
  jobsResultNumber: number;

  peoples: IPerson[];
  companies: ICompany[];
  jobs: IJob[];

  constructor(private searchService: SearchService, private router: Router) {}

  ngOnInit() {
    this.getPeople();
    this.getCompanies();
    this.getJobs();
    this.searchService.onUserSearchForCity().subscribe(city => {
      this.isFetched = false;
      this.getPeople({city});
      this.getCompanies({city});
      this.getJobs({city});
    });
  }

  getPeople(searchQueryParamerters?: any) {
    this.searchService.fetchSearchPeoples(searchQueryParamerters).subscribe((data: IPerson[]) => {
      this.isFetched = true;
      this.peopleResultNumber = data.length;
      if (data.length) {
        const mapData = data.map((person: IPerson) => {
          if (person.avatar === null) {
            person['personImgSrc'] = `../../../assets/img/compay-default-small.png`;
          } else {
            person['personImgSrc'] = person.avatar['url'];
          }
          return { ...person };
        });
        return (this.peoples = mapData);
      }
      return (this.hasNoContent = true);
    });
  }

  getCompanies(searchQueryParamerters?: any): void {
    this.searchService.fetchSearchCompanies(searchQueryParamerters).subscribe((companies: ICompany[]) => {
      this.isFetched = true;
      this.companiesResultNumber = companies.length;
      if (companies.length) {
        const mapedData = companies.map((company: ICompany) => {
          if (company.avatar === null) {
            company['companyImgSrc'] = `../../../assets/img/compay-default-small.png`;
          } else {
            company['companyImgSrc'] = company.avatar['url'];
          }
          return { ...company };
        });
        return (this.companies = mapedData);
      }
      return (this.hasNoContent = true);
    });
  }

  getJobs(searchQueryParamerters?: any): void {
    this.searchService.fetchSearchJobs('api/search/job/', searchQueryParamerters).subscribe((jobs: IJob[]) => {
      this.isFetched = true;
      this.jobsResultNumber = jobs.length;
      if (jobs.length) {
        return (this.jobs = jobs);
      }
      return (this.hasNoContent = true);
    });
  }

  /* get Jobs by id */
  navigateToDetails(id: number) {
    this.router.navigate(['/search/jobs/', id]);
  }

  updateUrl($event) {
    $event['srcElement']['src'] = `../../../assets/img/compay-default-small.png`;
  }
}
