import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ProfileService } from 'src/app/providers/profile/profile.service';
import { ActivityBodyComponent } from '../../wall/sub-components/activity-body/activity-body.component';

@Component({
  selector: 'app-albums-page',
  templateUrl: './albums-page.component.html',
  styleUrls: ['./albums-page.component.css', '../../../../assets/css/pages/profile.css']
})
export class AlbumsPageComponent implements OnInit {
  @ViewChild('talktoActivityBOOK') talktoActivityBOOK: ActivityBodyComponent;
  id;
  currentAlbum;
  albumPosts;
  slug;
  current_user;
  showGallery = true;
  admin = false;
  defaultImageBath;
  constructor(private activatedRoute: ActivatedRoute, private profileServ: ProfileService, private router: Router) {

  }

  ngOnInit() {
    this.id = this.activatedRoute.snapshot.paramMap.get("id");
    this.slug = this.activatedRoute.parent.params['value'].slug;
    this.getAlbumData();
    this.current_user = JSON.parse(localStorage.getItem('user'));
    if (this.current_user.slug === this.slug) {
      this.admin = true;
    }
  }
  toState() {
    this.router.navigate([`/profile/${this.slug}/photos`]);
  }
  deleteImage(image) {
    let confirmDeletion = confirm('Are you sure?');
    if (confirmDeletion) {
      this.profileServ.hideImage(image.activity_attachment[0].activity, {}).subscribe(res => {
        this.getAlbumImages();
      }), err => {
        console.log(err);
      }
    } else {
      return false;
    }
  }
  getAlbumImages() {
    this.profileServ.getAlbumDetails(this.slug, this.id).subscribe((res: any) => {
      this.albumPosts = res;
      // const albumPosts = Object.assign([], res);
      // this.tempPosts = Array.from(albumPosts);
    })
  }
  postsView() {
    // const newArr = this.albumPosts.concat();
    const dupArray = JSON.parse(JSON.stringify(this.albumPosts))
    this.talktoActivityBOOK.getAlbumImages(dupArray);
  }
  getAlbumData() {
    this.profileServ.getAlbumData(this.slug, this.id).subscribe((res) => {
      this.currentAlbum = res[0];
      this.getAlbumImages()
    })
  }
}
