import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { ProfileService } from 'src/app/providers/profile/profile.service';
import { CharacterizationDataModel, LookUpsModel } from 'src/app/models/interfaces';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-user-data',
  templateUrl: './user-data.component.html',
  styleUrls: ['../../../assets/css/pages/profile.css', './user-data.component.css']
})
export class UserDataComponent implements OnInit {
  slug;
  CharacterizationData: CharacterizationDataModel;
  oldCharacterizationData: CharacterizationDataModel;
  editGender; editAge; editHeight; editWeight; editBuild; editHair; editEye; editEthnicity;
  editData = false;
  editCharacterization = false;
  uploadingCharacterization = false;
  ethnicityName;
  current_user;
  admin = false;
  uploadingHoppy = false;
  lookUps: LookUpsModel = {
    'age_range': [],
    'height_range': [],
    'weight_range': [],
    'build': [],
    'hair': [],
    'eye': [],
    'ethnicities': [],
    'hobbies': []
  };
  editHobby = false;
  hobbies;
  selectedHobbies: number[] = [];
  constructor(private _profile: ProfileService, private route: ActivatedRoute) { }

  ngOnInit() {
    this.getProfileData();
    this.slug = this.route.parent.params['value']['slug'];
    this.getLookUpList();
    this.current_user = JSON.parse(localStorage.getItem('user'));
    if (this.current_user.slug === this.slug) {
      this.admin = true;
    }
  }
  getProfileData() {
    this._profile.profileData$.subscribe(res => {
      if (res) {
        this.CharacterizationData = {
          'gender': res.gender,
          'age_from': res.age_from,
          'age_to': res.age_to,
          'height': res.height || { id: null },
          'weight': res.weight || { id: null },
          'build': res.build || { id: null },
          'hair': res.hair || { id: null },
          'eye': res.eye || { id: null },
          'ethnicity': res.ethnicity,
          'users_profile_hobbies': res.users_profile_hobbies
        }
        this.hobbies = res['users_profile_hobbies'];
        this.mapHobbies()
        this.oldCharacterizationData = Object.assign({}, this.CharacterizationData);
      }
    })
  }
  mapHobbies() {
    this.hobbies
    this.hobbies.forEach(hobby => {
      // this.selectedHobbies.push(hobby.hobbies.id);
      // @Toni
      this.selectedHobbies.push(hobby.id);
    });
  }
  saveHoppy(event) {
    this.uploadingHoppy = true;
    // let hobbiesArray = [];
    // this.selectedHobbies.forEach((key) => {
    //   hobbiesArray.push({ 'hobbies': key });
    // });
    this._profile.postHoppies(this.slug, event).subscribe((res) => {
      this.getPofileDataInit(this.slug);
      this.editHobby = false;
      this.uploadingHoppy = false;
      setTimeout(() => {
        this.editHobby = true;
        this.uploadingHoppy = true;
      }, 250);
    }, err => this.uploadingHoppy = false);
  }
  getLookUpList() {
    this._profile.getLookUps()
      .subscribe((resL: any) => {
        Object.keys(resL).forEach((lookup) => {
          console.log(resL);
          this.lookUps[lookup] = [];
          resL[lookup].forEach((lookupData) => {
            this.lookUps[lookup] = [...this.lookUps[lookup],
            { id: lookupData['id'], name: lookupData['name'] }
            ];
          });
          if (this.CharacterizationData.ethnicity) { this.ethnicityName = this.getEthnicitiesText(this.CharacterizationData); }
        });
        // this.getPofileDataInit(this.slug);
      });
  }
  addHobby(event) {
    this.saveHoppy({ 'hobbies': event });
  }
  selectedVal(value: any, key) {
    this.CharacterizationData[key] = value;
  }
  getUserHobbyId(hobbyId) {
    return this.hobbies.find(element => {
      // if (element['hobbies'].id == hobbyId) {
      //   return element.id;
      // }
      // @Toni 
      if (element.id == hobbyId) {
        return element.id;
      }
    });
  }
  removeHobbie(event) {
    const selectedHobby = this.getUserHobbyId(event.value);
    this._profile.deleteHoppies(this.slug, selectedHobby.id).subscribe(res => {
      this.editHobby = false;
      this.uploadingHoppy = false;
      this.getPofileDataInit(this.slug);
      setTimeout(() => {
        this.editHobby = true;
        this.uploadingHoppy = true;
      }, 250);
    });
  }
  onChangeAgeValue(e) {
    // if (this.CharacterizationData) {
    //   this.CharacterizationData.age = e.target.value;
    // }
  }
  getPofileDataInit(slug) {
    // this.updateProfile.emit(slug);
    this._profile.getProfile(this.slug).subscribe((res: any) => {
      this._profile.syncProfileData(res);
      console.log(res);
    });
  }
  // save Data
  saveCharacterizationData(keyValue) {
    this.uploadingCharacterization = true;
    var params;
    if (keyValue == 'gender' || keyValue == 'ethnicity') {
      params = { [keyValue]: this.CharacterizationData[keyValue] };
    } else if (keyValue == 'age') {
      params = { 'age_from': this.CharacterizationData['age_from'], 'age_to': this.CharacterizationData['age_to'], };
    } else {
      params = { [keyValue]: this.CharacterizationData[keyValue]["id"] };
    }

    this._profile.patchProfile(this.slug, params).subscribe((res) => {
      this.oldCharacterizationData = this.CharacterizationData;
    }, err => {
      this.CharacterizationData = Object.assign({}, this.oldCharacterizationData);
    });
    this.editCharacterization = false;
    this.uploadingCharacterization = false;
  }
  // Cancel Edit
  cancelCharacterizationData() {
    // this.editCharacterization = false;
    this.CharacterizationData = Object.assign({}, this.oldCharacterizationData);
  }
  toggleInputs(visibleInput) {
    this.editGender = false; this.editAge = false; this.editHeight = false;
    this.editBuild = false; this.editHair = false; this.editEye = false;
    this.editEthnicity = false; this.editWeight = false;
    this["edit" + visibleInput] = true;
  }
  getEthnicitiesText(userInfo) {
    let result;
    this.lookUps.ethnicities.forEach((elem) => {
      if (elem.id === userInfo.ethnicity) {
        result = elem.name;
        return;
      }
    });
    return result;
  }
}
