import { Component, OnInit, Input, ViewEncapsulation } from '@angular/core';
import { SocialModel } from 'src/app/models/interfaces';
import { ProfileService } from 'src/app/providers/profile/profile.service';

@Component({
  selector: 'app-social-user-info',
  templateUrl: './social-user-info.component.html',
  styleUrls: ['./social-user-info.component.css', '../../../assets/css/pages/profile.css']
})
export class SocialUserInfoComponent implements OnInit {
  @Input() social;
  @Input() slug;
  @Input() admin;
  editSocial = false;
  uploadingSocial = false;
  oldSocial: SocialModel;
  // can_contact_info;
  // switch;
  constructor(private _profile: ProfileService) {
    // this._profile.profileSettings$.subscribe(res => {
    //   this.can_contact_info = res.can_contact_info;
    //   this.switch = localStorage.getItem('switch');
    // })
  }

  ngOnInit() {
    this.oldSocial = this.social;
    this.modifySocialData();
  }
  modifySocialData() {
    for (const key in this.social) {
      if (this.social.hasOwnProperty(key)) {
        const element = this.social[key];
        if (key !== 'mail' && key !== 'phone') {
          this.social[key]['url'] = this.socialCleanPattern(element);
        }
      }
    }
  }
  socialCleanPattern(element) {
    switch (element.network) {
      case 'facebook':
        return element.url.slice(25);
      case 'linkedin':
        return element.url.slice(24);
      case 'youtube':
        return element.url.slice(24);
      case 'twitter':
        return element.url.slice(24);
      case 'instagram':
        return element.url.slice(26);
      case 'snapchat':
        return element.url.slice(26);
      case 'google':
        return element.url.slice(23);
      default:
        break;
    }
  }
  cancelSocialEdit() {
    this.social = Object.assign({}, this.oldSocial);
    this.editSocial = false;
    this.uploadingSocial = false;
  }
  updateSocialApi(formInput) {
    this._profile.updateSocial(this.slug, formInput)
      .subscribe((res: any) => {
        this.editSocial = false;
        this.uploadingSocial = false;
        // res['url'] = this.socialCleanPattern(res);
        for (const key in this.social) {
          if (this.social.hasOwnProperty(key)) {
            const element = this.social[key];
            if (key == res.network) {
              this.social[key] = res;
              this.social[key]['url'] = this.socialCleanPattern(res);
            }
          }
        }
        this.oldSocial = this.social;
      },
        error => {
          this.editSocial = false;
          this.uploadingSocial = false;
        });
  }
  savePhone(val) {
    this._profile.patchProfile(this.slug, { 'phone': val }).subscribe((res) => {
    }, err => {
      this.cancelSocialEdit();
    });
    this.editSocial = false;
    this.uploadingSocial = false;
  }
  saveSocial(formInput, pattern) {
    const onlysocial = Object.assign({}, this.social);
    delete onlysocial.mail;
    delete onlysocial.phone;
    this.uploadingSocial = true;
    formInput.url = pattern + formInput.url;
    if (!formInput['id']) {
      this._profile.postSocial(this.slug, formInput).subscribe((res: any) => {
        this.updateSocialApi(res);
      }, (error) => {
        if (error) {
          this.cancelSocialEdit();
          this.editSocial = false;
          this.uploadingSocial = false;
        }
      });
    } else {
      this.updateSocialApi(formInput);
    }
  }

}
