import {Component, Input, OnInit} from '@angular/core';
import {WallService} from '../../../../services/api/wall.service';

@Component({
  selector: 'app-friend-requests-card',
  templateUrl: './friend-requests-card.component.html',
  styleUrls: ['../../../../../assets/css/pages/sidebar-friendsRequest.css']
})
export class FriendRequestsCardComponent implements OnInit {
  @Input() current_user;
  @Input() myFriendsCount;
  friendsRequests = [];
  friendsRequestsCount;
  friendsRequestsNext;
  errorMessage;
  successMessage;
  defaultImageBath;
  constructor(private wallApiService: WallService) {
  }

  ngOnInit() {
    this.getFriendRequests();
  }

  getFriendRequests() {
    this.wallApiService.getFriendsRequests(this.current_user.slug).subscribe((res) => {
      if (res) {
        this.friendsRequests = res['results'];
        this.friendsRequestsCount = res['count'];
        this.friendsRequestsNext = res['next'];
      }
    });
  }

  rejectFriendReq(slug, index) {
    this.wallApiService.rejectFriendsRequest(slug).subscribe((res) => {
      this.friendsRequests.splice(index, 1);
      this.friendsRequestsCount -= 1;
      this.getFriendRequests();
    }, () => {
      this.errorMessage = 'some thing went wrong';
    });
  }

  acceptFriendReq(slug, index) {

    this.wallApiService.acceptFriendsRequest(slug).subscribe((res) => {
      this.friendsRequests.splice(index, 1);
      this.friendsRequestsCount -= 1;
      this.getFriendRequests();
      this.myFriendsCount += 1;
    }, () => {
      this.errorMessage = 'some thing went wrong';
    });
  }

}
