import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({providedIn: 'root'})
export class GoogleMapsApiService {
    private baseApi = 'https://maps.googleapis.com/maps/api/';
    private key = 'AIzaSyCv5QHOLPZVpiaR74O7Yg23oaRsRoqmG1g';
    constructor (private http: HttpClient) { }

    getLocationByCoordsEn(lat , lng) {
        return this.http.get(this.baseApi + 'geocode/json?latlng=' + lat + ',' + lng + '&language=en' +  '&key=' + this.key);
    }
}