import {Component, EventEmitter, Input, OnInit, Output, ViewEncapsulation} from '@angular/core';
import {WallService} from '../../../../../../services/api/wall.service';

@Component({
  selector: 'app-create-comment',
  templateUrl: './create-comment.component.html'
})
export class CreateCommentComponent implements OnInit {
  @Input() current_user;
  @Input() post;
  @Input() posts;
  @Input() postIndex;
  @Input() commentId;
  @Input() commentIndex;
  @Output() threadSaved = new EventEmitter();
  @Input() friends = [];
  errorMessage;
  successMessage;
  newComment;
  emoji = false;
  mentions = [];
  defaultImageBath;
constructor(private wallApiService: WallService) {
}
  ngOnInit() {
  }
  addMention = (event) => {
    console.log(event);
    this.mentions.push({auth_user: event});
    console.log(this.mentions);
    return '@' + event.name + ' ';
  };

  comment() {
    if (this.commentId) {
      this.saveThread();
    } else {
      this.sendComment();
    }
  }

  sendComment() {
  this.validateMentions();
    this.post.uploadingComment = true;
    const body: any = {
      'comment': this.newComment,
      'thread': '',
      'auth_user': this.current_user.id,
      'activity': this.post.id
    };
    if (this.mentions.length) {
      body.mentions = this.mentions;
    }
    this.wallApiService.postComment(this.post.id , body).subscribe((res) => {
      this.newComment = '';
      this.posts[this.postIndex].PostComment = '';
      if (!this.posts[this.postIndex].comments) {
        this.posts[this.postIndex].comments = [];
      }
      // this.posts[postIndex].comments = [...this.posts[postIndex].comments, res];
      this.posts[this.postIndex]['comments'].unshift(res);
      this.post.uploadingComment = false;
    }, err => {
      this.post.uploadingComment = false;
      this.errorMessage = 'your comment didnot be sent please try again';

    });
  }
  validateMentions() {
  const mentions = [];
  this.mentions.forEach(meniton => {
    if (this.newComment.indexOf('@' + meniton.auth_user.name) !== -1) {
      mentions.push({auth_user: meniton.auth_user.pk});
    }
  });
  this.mentions = mentions;
  }

  saveThread() {
    this.validateMentions();
    // this.posts[postIndex].uploadingComment = true;
    const body: any = {
      'comment': this.newComment,
      'thread': this.commentId,
      'auth_user': this.current_user.id,
      'activity': this.post.id
    };
    if (this.mentions.length) {
      body.mentions = this.mentions;
    }
    this.wallApiService.postComment(this.post.id, body).subscribe((res) => {
      this.newComment = '';
      this.posts[this.postIndex].comments[this.commentIndex].postThread = '';
      if (!this.posts[this.postIndex].comments[this.commentIndex]['comment_thread']) {
        this.posts[this.postIndex].comments[this.commentIndex]['comment_thread'] = [];
      }
      // this.posts[postIndex].comments = [...this.posts[postIndex].comments, res];
      this.posts[this.postIndex].comments[this.commentIndex]['comment_thread'].unshift(res);
      // this.posts[postIndex]['uploadingComment'] = false;
      this.threadSaved.emit();
    }, err => {
      // this.posts[postIndex]['uploadingComment'] = false;
      this.errorMessage = 'your comment didnot be sent please try again';

    });
  }

  upendEmoji(event) {
    this.newComment = this.newComment ? this.newComment + event.emoji.native : event.emoji.native;
    console.log(event);
  }
}
