import {Injectable, OnDestroy} from '@angular/core';
import {HttpClient, HttpParams} from '@angular/common/http';
import {Observable, Subject} from 'rxjs/index';
import {map, takeUntil} from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class HttpService implements OnDestroy {
  situationUrl = '';
  unsubscribeGet$ = new Subject();
  unsubscribePost$ = new Subject();

  constructor(private http: HttpClient) {
  }

  setParameters(queryParameters) {
    let params = new HttpParams();
    for (const key in queryParameters) {
      if (queryParameters.hasOwnProperty(key) && queryParameters[key] !== '') {
        if (!params.has(key)) {
          params = params.set(key, queryParameters[key]);
        }
      }
    }
    return params;
  }

  /**
   * @param  {string} url                  link of target end point
   * @param  {object} queryParameters      Query that want to parse in the url
   */
  get(url: string, queryParameters?: object, header?): Observable<any> {
    const params = this.setParameters(queryParameters);
    return this.http
      .get(`${url}?${params.toString()}`, header)
      .pipe(takeUntil(this.unsubscribeGet$));
  }

  /**
   * @param  {string} url                  link of target end point
   * @param  {object} queryParameters      Query that want to parse in the url
   * @param  {any} body?                   payload that you want to send and it's optional
   */
  post(url: string, queryParameters?: object, body?: any) {
    const params = this.setParameters(queryParameters);
    return this.http.post(url, body).pipe(
      map(data => {
        return data;
      }),
      takeUntil(this.unsubscribePost$)
    );
  }

  delete(url: string) {
    return this.http.delete(url).pipe(
      map(data => {
        return data;
      }),
      takeUntil(this.unsubscribePost$)
    );
  }

  ngOnDestroy(): void {
    this.unsubscribeGet$.next();
    this.unsubscribeGet$.complete();
    this.unsubscribePost$.next();
    this.unsubscribePost$.complete();
  }
}
