import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivityBodyComponent } from '../../wall/sub-components/activity-body/activity-body.component';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-video',
  templateUrl: './video.component.html',
  styleUrls: ['./video.component.css']
})
export class VideoComponent implements OnInit {
  @ViewChild(ActivityBodyComponent) talktoActivityV;
  audioVideo;
  audioOnly = false;
  current_user;
  slug;
  admin;
  constructor(private route: ActivatedRoute) { }

  ngOnInit() {
    this.slug = this.route.parent.params['value']['slug'];
    this.current_user = JSON.parse(localStorage.getItem('user'));
    if (this.current_user.slug === this.slug) {
      this.admin = true;
    }
    this.audioVideo = 'video';
    this.audioOnly = true;
    this.talktoActivityV.loadVideoOnly(this.slug);
  }

}
