import { Component, HostListener, OnInit, ViewChild } from '@angular/core';
import { SearchService } from '../services/search.service';
import { IPerson } from '../interfaces/person.interface';
import { ILookups } from '../interfaces/lookups.interface';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Options } from 'ng5-slider';
import { MapSearchQuery } from '../utils/mapSearchQuery';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-people',
  templateUrl: './people.component.html',
  styleUrls: ['../../../../assets/css/pages/search-people.css']
})
export class PeopleComponent implements OnInit {
  searchPeopleForm: FormGroup;
  lookups: ILookups = {
    height_range: [],
    weight_range: [],
    build: [],
    hair: [],
    eye: [],
    ethnicities: [],
    hobbies: []
  };
  defaultImageBath;
  people: IPerson[];
  selectedUser: IPerson;
  isFetched: boolean;
  peopleResultNumber: number;
  hasNoContent: boolean;

  minValue = 18;
  maxValue = 28;
  options: Options;
  tags: number[] = [];

  dropdownListContent: ILookups;
  dropdownSettings = {};


  @ViewChild('user') userElement;

  @HostListener('window:scroll', ['$event'])
  scroll() {
    if (window.innerHeight + window.scrollY >= document.body.offsetHeight) {
      console.log(this.searchService.peoplesNext)
      // you're at the bottom of the page
      if (
        this.searchService.peoplesNext &&
        this.searchService.peoplesNext !== null &&
        this.searchService.peoplesNext !== undefined
      ) {
        this.searchService.fetchNextPeople(this.searchService.peoplesNext).subscribe((data: IPerson[]) => {
          this.peopleResultNumber += data.length;
          if (data.length) {
            const mapData = data.map((person: IPerson) => {
              if (person.avatar === null) {
                person['personImgSrc'] = '../../../../assets/img/compay-default-small.png';
              } else {
                person['personImgSrc'] = person.avatar['url'];
              }
              return { ...person };
            });
            return (this.people = { ...this.people, ...mapData });
          }
          return (this.hasNoContent = true);
        });
      }
    }
  }

  constructor(private _fb: FormBuilder, private searchService: SearchService, private router: Router) {
    this.searchService.fetchLookups().subscribe(lookups => {
      this.dropdownListContent = { ...lookups };
    });
  }
  navigateUser(user) {
    ;
    this.router.navigateByUrl(`/profile/${user.slug}`);
  }
  ngOnInit() {

    this.options = {
      floor: 0,
      ceil: 120
    };

    this.searchPeopleForm = this._fb.group({
      search: [''],
      fname: [''],
      lname: [''],
      ethnicity: [''],
      build: [''],
      hair: [''],
      hobbies: [''],
      height: [''],
      weight: [''],
      eye: [''],
      has_photo: [''],
      has_video: [''],
      has_audio: ['']
    });
    this.getPeople();

    this.searchService.onUserSearchForCity().subscribe(city => {
      this.isFetched = false;
      this.getPeople({ city });
    });

    this.dropdownSettings = {
      singleSelection: false,
      idField: 'id',
      textField: 'name',
      selectAllText: 'Select All',
      unSelectAllText: 'UnSelect All',
      itemsShowLimit: 3,
      allowSearchFilter: true
    };
  }

  /**
   * get people based on query parameter
   */
  getPeople(searchQueryParamerters?: any) {
    this.searchService.fetchSearchPeoples(searchQueryParamerters).subscribe((data: IPerson[]) => {
      this.isFetched = true;
      this.peopleResultNumber = data.length;
      if (data.length) {
        const mapData = data.map((person: IPerson) => {
          if (person.avatar === null) {
            person['personImgSrc'] = '../../../../assets/img/compay-default-small.png';
          } else {
            person['personImgSrc'] = person.avatar['url'];
          }
          return { ...person };
        });
        return (this.people = mapData);
      }
      return (this.hasNoContent = true);
    });
  }

  search() {
    this.hasNoContent = false;
    let searchQuery = this.searchPeopleForm.value;
    searchQuery['tags__in'] = this.tags.join('__');
    searchQuery['Age'] = this.minValue !== 18 || this.maxValue !== 28 ? `${this.minValue}__${this.maxValue}` : '';
    searchQuery = MapSearchQuery.mapSearchQuery(searchQuery);
    this.getPeople(searchQuery);
  }

  updateUrl($event) {
    $event['srcElement']['src'] = '../../../../assets/img/compay-default-small.png';
  }

  getTalentTypes(talentId: number) {
    const isExist = this.tags.indexOf(talentId) === -1;
    const index = this.tags.findIndex(ctalentId => ctalentId === talentId);
    if (!isExist) {
      return this.tags.splice(index, 1);
    }
    this.tags.push(talentId);
  }

  /** show or hide column by class name **/
  showHideDetails(user: IPerson) {
    this.selectedUser = user;
    this.selectedUser['selected'] = user['selected'];
    for (let i = 0; i < 12; i++) {
      if (this.people[i].id !== user.id) {
        this.people[i]['selected'] = false;
      }
    }
  }

  eidtCardViewStyle(index: number) {
    const userContainerWidth = this.userElement.nativeElement.offsetWidth;
    const width = userContainerWidth < 1000 ? ['0', '-240', '-480', '-720'] : ['0', '-290', '-580', '-875'];
    const firstThumbnial = (index + 4) % 4 === 0;
    const secondThumbnial = (index + 4) % 4 === 1;
    const thirdhumbnial = (index + 4) % 4 === 2;
    const forthThumbnial = (index + 4) % 4 === 3;
    let targetWidth: string;

    if (firstThumbnial) {
      targetWidth = width[0] + 'px';
    } else if (secondThumbnial) {
      targetWidth = width[1] + 'px';
    } else if (thirdhumbnial) {
      targetWidth = width[2] + 'px';
    } else if (forthThumbnial) {
      targetWidth = width[3] + 'px';
    }
    return targetWidth;
  }

  reset() {
    this.searchPeopleForm.reset({ index: 0 });
  }
}
