import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { Router } from '@angular/router';
import { SettingsService } from 'src/app/services/api/settings.service';
import { GoogleMapsApiService } from 'src/app/services';

@Component({
  selector: 'app-landing-page',
  templateUrl: './landing-page.component.html',
  styleUrls: ['../../../assets/css/pages/index.css']
})
export class LandingPageComponent implements OnInit {
  defaultImageBath;
  jobsList = [];
  @ViewChild('email') email: ElementRef;
  constructor(private router: Router, private settingsServ: SettingsService, private mapCorr: GoogleMapsApiService) { }
  subscribe(event) {
    if (this.email.nativeElement.validity.valid) {
      const body = new FormData();
      body.append("email", this.email.nativeElement.value);
      this.settingsServ.subscribeEmail(body).subscribe(() => {
      });
    }
  }
  getJobs() {
    this.settingsServ.getRandomJobs().subscribe((res: any) => {
      this.jobsList = res;
      this.jobsList.forEach(job => {
        job['location'] =
          job['latitude'] || job['longitude']
            ? this.getLocation(job['latitude'], job['longitude'])
            : 'location , city';
      })

    });
  }
  getLocation(longitude: any, latitude: any) {
    this.mapCorr.getLocationByCoordsEn(longitude, latitude).subscribe(
      res => {
        // FIXME: parse location http://bit.ly/2HRuOAO
        console.log('location: ', res);
      }
    );
  }
  ngOnInit() {
    this.getJobs();
  }
  routeTo(routeLink) {
    this.router.navigate([`/account/${routeLink}`]);
  }
}
