import { Component, OnInit } from '@angular/core';
import {
  AuthService as Angular6AuthService,
  FacebookLoginProvider,
  GoogleLoginProvider
} from 'angular-6-social-login';
import { AuthService } from '../../providers/auth-login/auth.service';
import { Router } from '@angular/router';
import { SettingsService } from 'src/app/services/api/settings.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
})
export class LoginComponent implements OnInit {
  data = {
    name: '',
    password: ''
  };
  errorMessage: string;
  verify = false;

  constructor(
    private router: Router,
    private _login: AuthService,
    private _settingService: SettingsService,
    private socialAuthService: Angular6AuthService
  ) {
  }

  ngOnInit() {
    const token = localStorage.getItem('token');
    if (token) {
      this._login.verifyToken({
        'token': token
      }).subscribe((data: any) => {
        if (data.token) {
          this.router.navigate(['/wall']);
        } else {
          localStorage.clear();
        }
      }, err => localStorage.clear());
    }

  }

  onSubmit(form: any) {
    this.errorMessage = '';
    this._login.getToken({
      'username': form.controls['name'].value,
      'password': form.controls['password'].value
    })
      .subscribe((res: any) => {
        localStorage.setItem('token', res['token']);
        localStorage.setItem('user', JSON.stringify(res['user']));
        this.getProfileSettings(res['user']['slug']);
      },
        (err) => {
          if (err.error.non_field_errors[0] === 'E-mail is not verified.') {
            this.verify = true;
          } else {
            this.errorMessage = 'Username or Password are wrong';
          }
        });

  }
  getProfileSettings(slug: String) {
    this._settingService.getProfileSettings(slug).subscribe((res: any) => {
      localStorage.setItem('settings', JSON.stringify(res));
      this.router.navigate(['/wall']);
    });
  }

  public socialLogIn(socialPlatform: string) {
    let socialPlatformProvider;
    if (socialPlatform === 'facebook') {
      socialPlatformProvider = FacebookLoginProvider.PROVIDER_ID;
    } else if (socialPlatform === 'google') {
      socialPlatformProvider = GoogleLoginProvider.PROVIDER_ID;
    } else if (socialPlatform === 'twitter') {
      socialPlatformProvider = GoogleLoginProvider.PROVIDER_ID;
    }

    this.socialAuthService.signIn(socialPlatformProvider).then(userData => {
      console.log(socialPlatform + ' sign in data : ', userData);
      // this._login.apiConnection(userData);
    });
  }
}
