import { Component, OnInit, HostListener, ViewChild, ElementRef } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { SearchService } from '../services/search.service';
import { IJob } from '../interfaces/job.interface';
import { Router, ActivatedRoute } from '@angular/router';
import { GoogleMapsApiService } from 'src/app/services';
import { Options } from 'ng5-slider';
import {MapSearchQuery} from '../utils/mapSearchQuery';

@Component({
  selector: 'app-jobs',
  templateUrl: './jobs.component.html',
  styleUrls: ['../../../../assets/css/pages/search-job.css']

})
export class JobsComponent implements OnInit {
  searchJobsForm: FormGroup;

  isFetched: boolean;

  hasNoContent: boolean;
  jobsResultNumber: number;
  jobs: IJob[];
  jobsNext: string;
  @ViewChild('jobsElem') jobsElem: ElementRef;
  jobsElemState: boolean;

  hasNoContentSuitMe: boolean;
  jobsSuitMeResultNumber: number;
  jobsSuitMe: IJob[];
  jobsNextSuitMe: string;
  @ViewChild('jobsSuitMeElem') jobsSuitMeElem: ElementRef;
  jobsSuitMeElemState: boolean;

  hasNoContentApplied: boolean;
  jobsAppliedResultNumber: number;
  jobsApplied: IJob[];
  jobsNextApplied: string;
  @ViewChild('jobsAppliedElem') jobsAppliedElem: ElementRef;
  jobsAppliedElemState: boolean;

  hasNoContentCreated: boolean;
  jobsCreatedResultNumber: number;
  jobsCreated: IJob[];
  jobsNextCreated: string;
  @ViewChild('jobsCreatedElem') jobsCreatedElem: ElementRef;
  jobsCreatedElemState: boolean;

  selector = '.Search-Jobs-Value';
  slug: any;
  tags: string;

  minValue = 1000;
  maxValue = 50000;
  options: Options = {
    floor: 0,
    ceil: 50000,
    step: 10
  };
  skills: Object[];
  dropdownSettings = {};

  @HostListener('window:scroll', ['$event'])
  scroll() {

    this.jobsElemState = this.jobsElem.nativeElement.classList.value === ' active show' ? true : false;
    this.jobsSuitMeElemState = this.jobsSuitMeElem.nativeElement.classList.value === 'active show' ? true : false;
    this.jobsAppliedElemState = this.jobsAppliedElem.nativeElement.classList.value === 'active show' ? true : false;
    this.jobsCreatedElemState = this.jobsCreatedElem.nativeElement.classList.value === 'active show' ? true : false;

    if (window.innerHeight + window.scrollY >= document.body.offsetHeight) {

      if (this.jobsNext !== null && this.jobsNext !== undefined && this.jobsElemState) {
        this.searchService.fetchNextJobs(this.jobsNext).subscribe((jobs: IJob[]) => {
          this.jobsElemState = false;
          this.jobsResultNumber += jobs.length;
          if (jobs.length) {
            // get interview place
            jobs.map(async job => {
              job['location'] =
                job['latitude'] || job['longitude'] ? await this.getLocation(job['latitude'], job['longitude']) : '';
            });
            this.jobsNext = jobs[0].jobsNext;
            return (this.jobs = [...this.jobs, ...jobs]);
          }
          return (this.hasNoContent = true);
        });
      } else if (this.jobsNextSuitMe !== null && this.jobsNextSuitMe !== undefined && this.jobsSuitMeElemState) {

        this.searchService.fetchNextJobs(this.jobsNextSuitMe).subscribe((jobs: IJob[]) => {
          this.jobsSuitMeElemState = false;
          this.jobsSuitMeResultNumber += jobs.length;
          if (jobs.length) {
            // get interview place
            jobs.map(async job => {
              job['location'] =
                job['latitude'] || job['longitude'] ? await this.getLocation(job['latitude'], job['longitude']) : '';
            });
            this.jobsNextSuitMe = jobs[0].jobsNext;
            return (this.jobsSuitMe = [...this.jobs, ...jobs]);
          }
          return (this.hasNoContentSuitMe = true);
        });
      } else if (this.jobsNextCreated !== null && this.jobsNextCreated !== undefined && this.jobsCreatedElemState) {

        this.searchService.fetchNextJobs(this.jobsNextCreated).subscribe((jobs: IJob[]) => {
          this.jobsCreatedElemState = false;
          this.jobsCreatedResultNumber += jobs.length;
          if (jobs.length) {
            // get interview place
            jobs.map(async job => {
              job['location'] =
                job['latitude'] || job['longitude'] ? await this.getLocation(job['latitude'], job['longitude']) : '';
            });
            this.jobsNextCreated = jobs[0].jobsNext;
            return (this.jobsCreated = [...this.jobs, ...jobs]);
          }
          return (this.hasNoContentCreated = true);
        });
      } else if (this.jobsNextApplied !== null && this.jobsNextApplied !== undefined && this.jobsAppliedElemState) {

        this.searchService.fetchNextJobs(this.jobsNextApplied).subscribe((jobs: IJob[]) => {
          this.jobsAppliedElemState = false;
          this.jobsAppliedResultNumber += jobs.length;
          if (jobs.length) {
            // get interview place
            jobs.map(async job => {
              job['location'] =
                job['latitude'] || job['longitude'] ? await this.getLocation(job['latitude'], job['longitude']) : '';
            });
            this.jobsNextApplied = jobs[0].jobsNext;
            return (this.jobsApplied = [...this.jobs, ...jobs]);
          }
          return (this.hasNoContentApplied = true);
        });
      }
    }
  }

  constructor(
    private _fb: FormBuilder,
    private searchService: SearchService,
    private router: Router,
    private route: ActivatedRoute,
    private mapCorr: GoogleMapsApiService
  ) {}

  ngOnInit() {
    this.slug = JSON.parse(localStorage.getItem('user'))['slug'];
    this.searchJobsForm = this._fb.group({
      search: [''],
      title: [''],
      tags__in: [''],
      is_female: [''],
      is_male: [''],
      have_daily_perks: [''],
      have_transportation: [''],
      have_meal: ['']
    });
    this.getAllKindOfJobs();

    this.searchService.onUserSearchForCity().subscribe(city => {
      this.isFetched = false;
      this.getJobs({city});
      this.getJobsCreated({city});
      this.getJobsApplied({city});
      this.searchService.fetchLookups().subscribe(lookups => {
        const hoobbies = lookups.hobbies.map(hobbie => hobbie['id']);
        this.skills = lookups.hobbies;
        this.tags = hoobbies.join('__');
        this.getJobsRecommend({ tags__in: this.tags, city });
      });
    });

    this.dropdownSettings = {
      singleSelection: false,
      idField: 'id',
      textField: 'name',
      selectAllText: 'Select All',
      unSelectAllText: 'UnSelect All',
      itemsShowLimit: 3,
      allowSearchFilter: true
    };

  }

  getAllKindOfJobs() {
    this.getJobs();
    this.getJobsCreated();
    this.getJobsApplied();
    this.searchService.fetchLookups().subscribe(lookups => {
      const hoobbies = lookups.hobbies.map(hobbie => hobbie['id']);
      this.skills = lookups.hobbies;
      this.tags = hoobbies.join('__');
      this.getJobsRecommend({ tags__in: this.tags });
    });
  }
  getJobs(searchQueryParamerters?: any): void {
    const URL_ENDPOINT = 'api/search/job/';
    this.searchService.fetchSearchJobs(URL_ENDPOINT, searchQueryParamerters).subscribe((jobs: IJob[]) => {
      this.isFetched = true;
      this.jobsResultNumber = jobs.length;
      if (jobs.length) {
        // get interview place
        jobs.map(async job => {
          job['location'] =
            job['latitude'] || job['longitude'] ? await this.getLocation(job['latitude'], job['longitude']) : '';
        });
        this.jobsNext = jobs[0].jobsNext;
        return (this.jobs = jobs);
      }
      return (this.hasNoContent = true);
    });
  }

  getJobsRecommend(searchQueryParamerters: any): void {
    const URL_ENDPOINT = 'api/search/job/';
    this.searchService.fetchSearchJobs(URL_ENDPOINT, searchQueryParamerters).subscribe((jobs: IJob[]) => {
      this.isFetched = true;
      this.jobsSuitMeResultNumber = jobs.length;
      if (jobs.length) {
        // get interview place
        jobs.map(async job => {
          job['location'] =
            job['latitude'] || job['longitude'] ? await this.getLocation(job['latitude'], job['longitude']) : '';
        });
        this.jobsNextSuitMe = jobs[0].jobsNext;
        return (this.jobsSuitMe = jobs);
      }
      return (this.hasNoContentSuitMe = true);
    });
  }

  /* Get Jobs Created */
  getJobsCreated(searchQueryParamerters?: any): void {
    const URL_ENDPOINT = 'search/jobs/created';
    this.searchService.fetchSearchJobs(URL_ENDPOINT, searchQueryParamerters).subscribe((jobs: IJob[]) => {
      this.isFetched = true;
      this.jobsCreatedResultNumber = jobs.length;
      if (jobs.length) {
        // get interview place
        jobs.map(async job => {
          job['location'] =
            job['latitude'] || job['longitude'] ? await this.getLocation(job['latitude'], job['longitude']) : '';
        });
        this.jobsNextCreated = jobs[0].jobsNext;
        return (this.jobsCreated = jobs);
      }
      return (this.hasNoContentCreated = true);
    });
  }

  /* Get Jobs Created */
  getJobsApplied(searchQueryParamerters?: any): void {
    const URL_ENDPOINT = 'search/jobs/applied';
    this.searchService.fetchSearchJobs(URL_ENDPOINT, searchQueryParamerters).subscribe((jobs: IJob[]) => {
      this.isFetched = true;
      this.jobsAppliedResultNumber = jobs.length;
      if (jobs.length) {
        // get interview place
        jobs.map(async job => {
          job['location'] =
            job['latitude'] || job['longitude'] ? await this.getLocation(job['latitude'], job['longitude']) : '';
        });
        this.jobsNextApplied = jobs[0].jobsNext;
        return (this.jobsApplied = jobs);
      }
      return (this.hasNoContentApplied = true);
    });
  }

  getLocation(longitude: any, latitude: any) {
    this.mapCorr.getLocationByCoordsEn(longitude, latitude).subscribe(res => {
      // FIXME: parse location http://bit.ly/2HRuOAO
      console.log('location : ', res);
    });
  }

  /* get Jobs by id */
  navigateToDetails(slug: string, job: string) {
    this.router.navigate(['/job-page/', slug, job ]);
  }

  createNewJob() {
    this.router.navigate(['../../job/create-job/', this.slug], { relativeTo: this.route });
  }

  getFormValue(control: any) {
    return this.searchJobsForm.get(control);
  }

  search() {
    this.hasNoContent = false;
    this.hasNoContentSuitMe = false;
    this.hasNoContentCreated = false;
    this.hasNoContentApplied = false;

    let searchQuery = this.searchJobsForm.value;
    searchQuery['salary'] =
      this.minValue !== 1000 || this.maxValue !== 50000 ? `${this.minValue}__${this.maxValue}` : '';
    searchQuery = MapSearchQuery.mapSearchQuery(searchQuery);
    searchQuery['tags__in'] = (this.searchJobsForm.value.tags__in !== '') ? this.searchJobsForm.value.tags__in.join('__') : '';

    this.getJobs(searchQuery);
    this.searchJobsForm.value.tags__in.length
      ? this.getJobsRecommend(searchQuery)
      : this.getJobsRecommend({ tags__in: this.tags });
    this.getJobsCreated(searchQuery);
    this.getJobsApplied(searchQuery);
  }

  reset() {
    this.searchJobsForm.reset({index: 0});
  }
}
