import { Component, OnInit, Input } from '@angular/core';
import { WallService } from 'src/app/services/api/wall.service';

@Component({
  selector: 'app-banner-img',
  templateUrl: './banner-img.component.html',
  styleUrls: ['./banner-img.component.css']
})
export class BannerImgComponent implements OnInit {
  @Input('cover') profileCover;
  @Input() slug;
  @Input() admin;
  loadingCover = false;
  imgUpload = { status: '', message: 0, filePath: '' };
  constructor(private _httpWall: WallService) { }

  ngOnInit() {
  }

  resetCover() {
    this.profileCover = '';
    this.loadingCover = true;
    const input = new FormData();
    input.append('file', '');
    this._httpWall.resetCover(this.slug)
      .subscribe((data) => {
        this.profileCover = '';
        this.loadingCover = false;
      }, err => this.loadingCover = false);
  }

  uploadAvatar(event, imageType) {
    if (event.target.files.length > 0) {
      const file = event.target.files[0];
      const input = new FormData();
      input.append('file', file);
      const reader = new FileReader();
      reader.readAsDataURL(file); // read file as data url
      reader.onload = (ev) => {
        let url;
        if (file.type.includes('image')) {
          url = reader.result;
        } else {
          alert('file unsuported');
        }
        this.loadingCover = true;
        this._httpWall.patchCover(this.slug, input)
          .subscribe((data: any) => {
            console.log(data);
            if (data['status'] == "progress") {
              this.imgUpload = data;
            }
            // if (data['id']) {
            //   this.profileCover = url;
            //   this.loadingCover = false;
            //   this.imgUpload = { status: '', message: 0, filePath: '' };
            // }
            // @Toni 
            if (data.cover) {
              this.profileCover = data.cover;
              this.loadingCover = false;
              this.imgUpload = { status: '', message: 0, filePath: '' };
            }

          }, err => this.loadingCover = false);
      };
    }
  }
}
