  @HostListener('click', ['$event'])
  onClick(event: MouseEvent) {
    let exist = false;
    event['path'].filter((path) => {
      if (path.className == 'search-menu inline-block' || path.className == 'form-control searchBar') {
        exist = true;
        return exist;
      }
    })
    if (exist) {
      this.onSearchSelected = true;
    } else {
      this.onSearchSelected = false;

    }
    console.log(event.currentTarget);
  }
