  getCompanies() {
    this.companyExist = false;
    this.companyService.get(this.current_user.slug).subscribe(

      (res: any) => {
        console.log('res companies', res);
        this.companies = res.results;

        this.router.events.subscribe((event: Event) => {
          if (event instanceof NavigationEnd) {
            // console.log("event: *************start",this.route.snapshot.children[0]._urlSegment.segments[2].path);
            this.router.url
            let tree: UrlTree = this.router.parseUrl(this.router.url);
            let f = tree.fragment; // return 'fragment'
            let q = tree.queryParams; // returns {debug: 'true'}
            let g: UrlSegmentGroup = tree.root.children[PRIMARY_OUTLET];
            let s: UrlSegment[] = g.segments; // returns 2 segments 'team' and '33'
            let p1;
            if (s[2])
              p1 = s[2];
            else
              p1 = null;
            let p2;
            if (s[3])
              p2 = s[3];
            else
              p2 = null;
            if (p1 || p2) {
              console.log("hiiiiiiiiiiiiiiiiiiiiiiiii");

              let res1 = this.companies.filter(item => {
                return item.slug == p1 || item.slug == p2
              });
              console.log("reeeeeeeeeeeeeesssssssssssssssssssssssss", res1);

              if (res1.length > 0) {
                if (localStorage.getItem('switch') !== res1[0].slug) {
                  this.switchToCompany(res1[0].slug);
                  localStorage.setItem('switch', res1[0].slug);
                }
              }
              else {
                this.switchToUser();
                localStorage.removeItem('switch');
              }
            }
            else
              this.switchToUser();


          }
        });
        this.company = this.companies[0];
        this.companyExist = true;
        // this.check();
      }, err => {
        this.companyExist = false;
        this.company = null;
      }
    );
  }
