import { Component, Input, OnInit, EventEmitter } from '@angular/core';
import { HttpProgressEvent } from '@angular/common/http';
import { UploadOutput, UploadInput, UploadFile, humanizeBytes, UploaderOptions } from 'ngx-uploader';
import { trigger, state, style, animate, transition } from '@angular/animations';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ProfileService } from 'src/app/providers/profile/profile.service';
import { Router, ActivatedRoute } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
@Component({
  selector: 'app-albums',
  templateUrl: './albums.component.html',
  styleUrls: ['../../../../../assets/css/pages/album.css', './albums.component.css'],
  animations: [
    trigger('changeBarSize', [
      state('initial', style({
        width: '10%'
      })),
      state('final', style({
        width: '100%'
      })),
      transition('initial=>final', animate('10s')),
    ])]
})
export class AlbumsComponent implements OnInit {
  albumForm: FormGroup;
  btnDisabled = false;
  $getAllalbum;
  defaultImageBath;
  showAll: boolean = false;
  albums: any = [];
  firstFourAlbums: any = [];
  albumLimit: any = [];
  albumName: string = '';
  albumImage: any;
  albumsCount: number = 0;
  photosCount: number = 0;
  imageDesc: string = '';
  imagePreview: any;
  imageToUpload: any;
  imageResult: any;
  albumId: string;
  uploadPercentage: any = [];
  showProgress: boolean = false;
  newAlbum: boolean = false;
  albumsDescending: any = [];
  albumsAscending: any = [];
  albumNameForBinding: string = 'Untitled Album';
  photoFileUpload = { status: '', message: 0, filePath: '' };
  admin: boolean;
  profileSlug: any;
  current_user;
  constructor(private _httpProfile: ProfileService, private spinner: NgxSpinnerService, private router: Router, private route: ActivatedRoute) {
    this.profileSlug = this.route.parent.params['value']['slug'];
    this.current_user = JSON.parse(localStorage.getItem('user'));
    if (this.current_user.slug === this.profileSlug) {
      this.admin = true;
    } else { this.admin = false; }
    this.albumForm = new FormGroup({
      'albumName': new FormControl(null, [Validators.required]),
    });
  }
  ngOnInit() {
    this.spinner.show();
    this.getAllAlbums();
    this.getAlbumLimit();
  }
  showAllAlbums() {
    !this.showAll ? this.showAll = true : this.showAll = false;
  }
  getAllAlbums() {
    this._httpProfile.getAllAlbumsNames(this.profileSlug).subscribe(res => {
      this.albums = res;
      this.albums.sort((a, b) => b.id - a.id);
      console.log(this.albums);
      this.firstFourAlbums = this.albums.slice(0, 4);
      console.log(this.firstFourAlbums);
      this.albumsCount = this.albums.length;
      this.photosCount = 0;
      for (let i in this.albums) {
        this.photosCount += this.albums[i].item_count;
      }
    });
  }
  triggerAlbum() {
    let album = document.getElementById('album-upload');
    album.click();
  }
  triggerCreateAlbum() {
    let create = document.getElementById('album-create');
    create.click();
  }
  uploadAlbum(e) {
    this.uploadPercentage.push({ 'currentState': 'initial' });
    this.imageToUpload = e.target.files[0];
    const reader = new FileReader();
    this.photoFileUpload = { status: '', message: 0, filePath: '' };
    reader.readAsDataURL(this.imageToUpload); // read file as data url
    reader.onload = (ev) => {
      let url;
      if (this.imageToUpload.type.includes('video')) {
        this.imagePreview = '../../assets/vedio.png';
      } else if (this.imageToUpload.type.includes('audio')) {
        this.imagePreview = '../../assets/sound.jpeg';
      } else if (this.imageToUpload.type.includes('image')) {
        this.imagePreview = reader.result;
      } else {
        alert('file unsuported');
        this.imagePreview = null;
        // this.photos.splice(this.photos.length - 1, 1);
      }
      // this.submitImage();
    }
  }
  async submitImage() {
    this.showProgress = true;
    this.btnDisabled = true;
    const input = new FormData();
    input.append('file', this.imageToUpload);
    const content = new FormData();
    this._httpProfile.uploadImage(this.profileSlug, input).subscribe(async (res: any) => {
      this.uploadPercentage[this.uploadPercentage.length - 1]['currentState'] = 'final';
      if (res['status'] == "progress") {
        this.photoFileUpload = res;
      }
      if (res['id']) {
        this.imageResult = res;
        if (this.albumId != null) {
          await content.append('album_id', this.albumId);
        }
        content.append('content', this.imageDesc);
        this._httpProfile.updateAlbumImage(this.profileSlug, this.imageResult.id, content).subscribe(res => {
          this.showProgress = false;
          this.btnDisabled = false;
          this.imagePreview = null;
          this.imageDesc = '';
          this.getAlbumLimit();
          this.getAllAlbums();
        }), err => {
          console.log(err);
        }
      }
    }, error => {
      console.log(error);
      console.log({ file: this.imageToUpload });
      this.btnDisabled = false;
    });
  }
  createAlbum(e) {
    console.log(e.target.files[0]);
    this.albumImage = e.target.files[0];
    console.log(this.albumImage);

  }
  showNewAlbumForm() {
    this.newAlbum = !this.newAlbum;
  }
  navigateAlbum(album) {
    this.router.navigate([`/profile/${this.profileSlug}/albums/${album.id}`]);
  }
  createNewAlbum() {
    let album = document.getElementById('closeAddModal');
    let albumList = document.getElementById('closeAlbumListModal');
    const albumForm = new FormData();
    albumForm.append('album_name', this.albumForm.get('albumName').value);
    this._httpProfile.createAlbumName(this.profileSlug, albumForm).subscribe(res => {
      this.albumForm.reset();
      this.getAllAlbums();
      // album.click();
      // albumList.click();
      this.showNewAlbumForm();
    }), err => {
      console.log(err);
    }
  }
  getAllPhotos() {
    this.spinner.show();
    this._httpProfile.getAllPhotos(this.profileSlug).subscribe(res => {
      this.albumLimit = [];
      this.albumLimit = res;
      this.spinner.hide();
    }), err => {
      console.log(err);
    }
  }
  deleteImage(image, i) {
    let confirmDeletion = confirm('Are you sure?');
    if (confirmDeletion) {
      console.log(this.albumLimit, image.activity_attachment[0].activity);
      this._httpProfile.hideImage(image.activity_attachment[0].activity, {}).subscribe(res => {
        console.log(res);
        this.getAlbumLimit();
        this.getAllAlbums();
      }), err => {
        console.log(err);
      }
    } else {
      return 0;
    }
  }
  getAlbumLimit() {
    this._httpProfile.GetAlbumLimit(this.profileSlug).subscribe(res => {
      this.albumLimit = res;
      this.spinner.hide();
    }), err => {
      console.log(err);
    }
  }
  setAlbumId(album) {
    this.albumId = album.id;
    this.albumNameForBinding = album.album_name;
  }
}
