import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { HttpClient } from '@angular/common/http';
import 'rxjs/add/operator/catch';

@Injectable({
  providedIn: 'root'
})
export class SettingsService {
  apiUrl = environment.apiUrl + '/';
  token = localStorage.getItem('token');

  constructor(private _http: HttpClient) {
  }

  subscribeEmail(body) {
    return this._http.post(this.apiUrl + 'api/subscribe', body);
  }

  getProfileSettings(slug) {
    const url = this.apiUrl + `profile/${slug}/settings`;
    return this._http.get(url);
  }

  saveProfileSettings(slug, param) {
    const url = this.apiUrl + `profile/${slug}/settings`;
    return this._http.patch(url, param);
  }

  changePassword(body) {
    const url = this.apiUrl + `auth/password/change/`;
    return this._http.post(url, body, {
      headers: {
        'Authorization': `JWT ${this.token}`
      }
    })
  }

  getRandomJobs() {
    return this._http.get(this.apiUrl + 'jobs/random');
  }

  editEmail(modifiedEmail, originalEmail) {
    const url = this.apiUrl + `email/${originalEmail}/change`;
    return this._http.post(url, { "new_email": modifiedEmail });
  };

  verifyEmail(email) {
    const url = this.apiUrl + `email/${email}/verify`;
    return this._http.post(url, {});
  }

  createEmail(body) {
    const url = this.apiUrl + `email/new`;
    return this._http.post(url, body);
  }

  changePrimaryEmail(email) {
    const url = this.apiUrl + `email/${email}/primary`;
    return this._http.post(url, {});
  }
}
