import {Injectable, OnDestroy} from '@angular/core';
import {HttpService} from '../../../_services';
import {ILookups} from '../interfaces/lookups.interface';
import {environment} from 'src/environments/environment';
import {map, takeUntil} from 'rxjs/operators';
import {Observable, Subject} from 'rxjs';
import {IPerson} from '../interfaces/person.interface';
import {ICompany} from '../interfaces/company.interface';
import {IJob} from '../interfaces/job.interface';

@Injectable({
  providedIn: 'root'
})
export class SearchService implements OnDestroy {
  unsubscribeFetchLookups$ = new Subject();
  unsubscribeFetchSearchPeoples$ = new Subject();
  unsubscribeFetchSearchCompanies$ = new Subject();
  unsubscribeFetchSearchJobs$ = new Subject();
  unsubscribeFetchSearchJobDetails$ = new Subject();
  unsubscribeFetchNext$ = new Subject();

  public peoplesNext: string;

  private cityChangeSearchSubject: Subject<string>;

  constructor(private http: HttpService) {
    this.cityChangeSearchSubject = new Subject<string>();
  }

  handleCityChangeSearch(city: string): void {
    this.cityChangeSearchSubject.next(city);
  }

  onUserSearchForCity(): Observable<string> {
    return this.cityChangeSearchSubject;
  }

  /**
   * get lookups
   */
  fetchLookups(): Observable<ILookups> {
    return this.http.get(`${environment.apiUrl}/profile/lookups`).pipe(
      map((lookups: ILookups) => (lookups)),
      takeUntil(this.unsubscribeFetchLookups$)
    );
  }

  fetchNextJobs(url: string): Observable<IJob[]> {
    return this.http.get(url).pipe(
      map(this.mapSearchJobs),
      takeUntil(this.unsubscribeFetchNext$)
    );
  }

  fetchNextPeople(url: string): Observable<IPerson[]> {
    return this.http.get(url).pipe(
      map(this.mapSearchPeoples),
      takeUntil(this.unsubscribeFetchNext$)
    );
  }

  fetchNextCompanies(url: string): Observable<ICompany[]> {
    return this.http.get(url).pipe(
      map(this.mapSearchCompanies),
      takeUntil(this.unsubscribeFetchNext$)
    );
  }

  /* Get People */
  private mapSearchPeoples(data: any): IPerson[] {
    this.peoplesNext = data.next;
    return data.results.map((person: IPerson) => ({ selected: false, ...person }));
  }

  fetchSearchPeoples(searchKeyWords?: IPerson): Observable<IPerson[]> {
    return this.http.get(`${environment.apiUrl}/api/search/profile/`, searchKeyWords).pipe(
      map(this.mapSearchPeoples),
      takeUntil(this.unsubscribeFetchSearchPeoples$)
    );
  }

  /* Get Companies */
  private mapSearchCompanies(data: any): ICompany[] {
    const companiesNext = data.next;
    return data.results.map((company: ICompany) => ({ companiesNext, ...company }));
  }

  fetchSearchCompanies(searchKeyWords?: ICompany): Observable<ICompany[]> {
    return this.http.get(`${environment.apiUrl}/api/search/company/`, searchKeyWords).pipe(
      map(this.mapSearchCompanies),
      takeUntil(this.unsubscribeFetchSearchCompanies$)
    );
  }

  /* Get Companies */
  private mapSearchJobs(data: any): IJob[] {
    const jobsNext = data.next;
    return data.results.map((job: IJob) => ({ jobsNext, ...job }));
  }

  fetchSearchJobs(URL_ENDPOINT: string, searchKeyWords?: IJob): Observable<IJob[]> {
    return this.http.get(`${environment.apiUrl}/${URL_ENDPOINT}`, searchKeyWords).pipe(
      map(this.mapSearchJobs),
      takeUntil(this.unsubscribeFetchSearchJobs$)
    );
  }

  fetchSearchJobDetails(URL_ENDPOINT: string): Observable<IJob> {
    return this.http
      .get(`${environment.apiUrl}/${URL_ENDPOINT}`)
      .pipe(takeUntil(this.unsubscribeFetchSearchJobDetails$));
  }

  ngOnDestroy(): void {
    this.unsubscribeFetchLookups$.unsubscribe();
    this.unsubscribeFetchSearchPeoples$.unsubscribe();
    this.unsubscribeFetchSearchCompanies$.unsubscribe();
    this.unsubscribeFetchSearchJobDetails$.unsubscribe();
    this.unsubscribeFetchNext$.unsubscribe();
  }
}
