import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { SearchLandingComponent } from './search-landing.component';
import { PeopleComponent } from './people/people.component';
import { CompaniesComponent } from './companies/companies.component';
import { JobsComponent } from './jobs/jobs.component';
import { JobsDetailsComponent } from './components/jobs-details/jobs-details.component';

const routes: Routes = [
  {redirectTo:'people',path: '',
  pathMatch: 'full'},
  {
    path: 'results',
    component: SearchLandingComponent
  },
  {
    path: 'people',
    component: PeopleComponent
  },
  {
    path: 'companies',
    component: CompaniesComponent
  },
  {
    path: 'jobs',
    component: JobsComponent
  },
  {
    path: 'jobs/:id',
    component: JobsDetailsComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SearchRoutingModule { }
