import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { NotificationService } from 'src/app/services/api/notification.service';
import { ActivityBodyComponent } from '../../wall/sub-components/activity-body/activity-body.component';
import { SpinnerComponent } from 'src/app/_core/spinner/spinner.component';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-notication-activity',
  templateUrl: './notication-activity.component.html',
  styleUrls: ['./notication-activity.component.css']
})
export class NoticationActivityComponent implements OnInit {
  activityId;
  admin = false;
  current_user;
  post;
  posts = [];
  friends = [];
  profileSlug;
  profilePosts;
  @ViewChild('talktoActivityP') talktoActivityP: ActivityBodyComponent;
  constructor(private _activatedRoute: ActivatedRoute, private _notifyServ: NotificationService, private spinner: NgxSpinnerService) {

  }

  ngOnInit() {
    this._activatedRoute.params.subscribe(params => {
      this.activityId = params['id'];
      this.doOnInit();
    });
    // this.getActivity();
  }
  doOnInit() {
    this.getActivity();
  }
  isAdmin() {
    this.current_user = JSON.parse(localStorage.getItem('user'));
    this.profileSlug = this.current_user.slug;
    if (this.current_user.slug === this.post.auth_user.slug) {
      this.admin = true;
    }
  }
  resetValues() {
    this.admin = false;
    this.current_user;
    this.post;
    this.posts = [];
    this.friends = [];
    this.profileSlug;
    this.profilePosts;
  }
  getActivity() {
    this.spinner.show();
    this._notifyServ.getActivity(this.activityId).subscribe((res: any) => {
      if (res) {
        this.resetValues();
        this.posts.push(res);
        this.post = this.posts[0];
        this.talktoActivityP.getSpecificActivity(this.posts);
        this.profilePosts = this.posts;
        this.isAdmin();
        this.spinner.hide();
      }
    })
  }
}
