import { Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';
import { HttpClient } from '@angular/common/http';
@Injectable({
  providedIn: 'root'
})
export class NotificationService {
  apiUrl = environment.apiUrl + '/api/notification';

  constructor(private _http: HttpClient) { }
  getNotifications() {
    return this._http.get(this.apiUrl);
  }
  getNextNotifications(url) {
    return this._http.get(url);
  }
  filterNotifications(month, year) {
    return this._http.get(this.apiUrl + `?month=${month}&year=${year}`);
  }
  markAsRead(notifyId) {
    const url = this.apiUrl + `/${notifyId}/read`;
    return this._http.post(url, {});
  }
  getActivity(id) {
    let url = environment.apiUrl + `/activity/${id}`;
    return this._http.get(url);
  }
}
